.students-container {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
}

.students-container .students-header {
	width: 100%;
	padding: 1rem;
	display: flex;
	background-color: #fff;
	border-bottom: 1px solid #c5c5c5;
	border-radius: 5px 5px 0 0;
}

.students-container .students-header .students-stats {
	width: 80%;
	display: flex;
	flex-direction: column;
}

.students-container .students-header .students-stats h1 {
	font-size: 22px;
}

.students-container .students-header .students-stats .officer-stats {
	width: 100%;
	padding: 0.5rem 0;
	display: flex;
	align-items: center;
}

.students-container .students-header .students-stats .officer-stats p {
	font-size: 14px;
	color: #999999;
}

.students-container .students-header .students-stats .officer-stats p span {
	color: #414141;
	font-weight: 500;
	font-size: 16px;
}

.students-container .students-header .students-add {
	width: 20%;
	display: flex;
	justify-content: center;
}

.students-container .students-header .students-add .add-officer-button {
	width: 80%;
	height: 50%;
	padding: 0.5rem 1rem;
	background: linear-gradient(90deg, #3358f4, #1d8cf8);
	border-radius: 6px;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.5);
	transition: all 0.1s;
}

.students-container .students-header .students-add .add-officer-button:hover {
	box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.75);
}

.students-container .students-header .students-add .add-officer-button img {
	height: 20px;
	margin-right: 20px;
}

.students-container .students-header .students-add .add-officer-button p {
	color: #fff;
}

.students-container .grade-book-body {
	width: 100%;
	height: 100%;
	padding: 0.5rem;
	display: flex;
	justify-content: center;
	border-radius: 0 0 5px 5px;
	overflow-y: scroll;
}

.students-container .grade-book-body .grade-student-list {
	width: 280px;
	height: 75vh;
	overflow-y: scroll;
	padding: 0 1rem;
}

.students-container .grade-book-body .grade-student-list .student-grade-box {
	width: 100%;
	height: 220px;
	display: flex;
	flex-direction: column;
	background-color: #0041c2;
	box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.25);
	justify-content: flex-end;
	margin: 0.5rem 0;
}

.students-container .grade-book-body .grade-student-list .student-grade-box .student-grade-box-body {
	width: 100%;
	height: 150px;
	background-color: #fff;
	display: flex;
	flex-direction: column;
	position: relative;
	align-items: center;
	padding-top: 60px;
}

.students-container .grade-book-body .grade-student-list .student-grade-box .student-grade-box-body img {
	position: absolute;
	width: 80px;
	height: 80px;
	border-radius: 50%;
	top: -40px;
	overflow: hidden;
	object-fit: cover;
}

.students-container .grade-book-body .grade-student-list .student-grade-box .student-grade-box-body h1 {
	color: #0041c2;
	font-size: 1.25rem;
}

.students-container .grade-book-body .grade-student-list .student-grade-box .student-grade-box-body p {
	padding: 0.25rem 0.7rem;
	border: 1px solid #0041c2;
	border-radius: 15px;
	font-size: 0.7rem;
	cursor: pointer;
	color: #fff;
	background-color: #0041c2;
}

.students-container .grade-book-body .grade-container {
	width: calc(100% - 280px);
	/* height: 100%; */
	padding: 0.5rem;
	align-items: center;
	justify-content: center;
	border-radius: 0 0 5px 5px;
	overflow-y: scroll;
}

.students-container .grade-book-body .grade-container .grade-type-list {
	display: flex;
	align-items: center;
	width: 100%;
	justify-content: space-evenly;
	padding: 0.5rem 0;
}

.students-container .grade-book-body .grade-container .grade-type-list .grade-type {
	width: 120px;
	height: 35px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 10px;
	cursor: pointer;
}

.students-container .grade-book-body .grade-container .grade-type-list .grade-type p {
	font-size: 1rem;
}

.students-container .grade-book-body .grade-container .grade-type-list .grade-type.active {
	width: 120px;
	height: 35px;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #325cf5;
	border-radius: 10px;
}

.students-container .grade-book-body .grade-container .grade-type-list .grade-type.active p {
	font-size: 1rem;
	color: #fff;
}

.students-container .grade-book-body .grade-container .grade-book-list {
	display: flex;
	flex-wrap: wrap;
	width: 100%;
}

.students-container .grade-book-body .grade-container .grade-book-list .grade-book-box {
	display: flex;
	align-items: center;
	flex-direction: column;
	margin: 1rem;
	border: 1.5px solid #325cf5;
	padding: 1rem;
	border-radius: 5px;
	cursor: pointer;
	transform: translateY(0px);
	transition: transform 0.25s ease-in-out;
	width: 260px;
}

.students-container .grade-book-body .grade-container .grade-book-list .grade-book-box:hover {
	transform: translateY(-5px);
	transition: transform 0.25s ease-in-out;
}

.students-container .grade-book-body .grade-container .grade-book-list .grade-book-box h1 {
	font-size: 1.25rem;
	text-align: center;
}

.students-container .grade-book-body .grade-container .grade-book-list .grade-book-box p {
	font-size: 0.85rem;
	color: #464646;
	font-weight: 600;
}

.students-container .grade-book-body .grade-container .grade-book-list .grade-book-box p:last-child {
	font-size: 1rem;
	color: #325cf5;
}
