.discussion-room-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
}

.discussion-room-container .go-back {
    width: 100%;
    height: 10%;
    display: flex;
    align-items: center;
    padding: 0.5rem 0;
    z-index: 999;
}

.discussion-room-container .go-back p{
    font-size: 1.5rem;
    cursor: pointer;
}

.discussion-room-container .go-back p span {
    font-size: 16px;
}

.discussion-room-container .room-header {
    width: 100%;
    height: 12%;
    background-color: #DCECF3;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0.5rem 1rem;
    -webkit-box-shadow: 0px 5px 3px 0px rgba(0,0,0,0.1);
    -moz-box-shadow: 0px 5px 3px 0px rgba(0,0,0,0.1);
    box-shadow: 0px 5px 3px 0px rgba(0,0,0,0.1);
    z-index: 999;
}

.discussion-room-container .room-header .room-info {
    display: flex;
    flex-direction: column;
}

.discussion-room-container .room-header .room-info h3 {
    color: #3c8dbc;
    font-size: 18px;
    font-weight: 500;
}

.discussion-room-container .room-header .room-info h3 span {
    color: #464646;
    font-size: 16px;
}

.discussion-room-container .room-header .room-info p {
    color: #777777;
    font-size: 13px;
}

.discussion-room-container .room-header span {
    color: #3c8dbc;
    font-size: 24px;
    cursor: pointer;
}

.discussion-room-container .room-body {
    width: 100%;
    height: 68vh;
    background-color: #f5f7fa;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
}

.discussion-room-container .room-body .own-chat {
    width: 100%;
    padding: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-bottom: 10px;
}

.discussion-room-container .room-body .other-chat {
    width: 100%;
    padding: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 10px;
}

.discussion-room-container .add-msg-btn {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #3c8dbc;
    position: absolute;
    bottom: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.35);
    -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.35);
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.35);
}

.discussion-room-container .add-msg-btn p {
    color: #fff;
    margin: 0;
    font-size: 24px;
}

