.schools-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.schools-container .schools-header {
    width: 100%;
    padding: 1rem;
    display: flex;
    background-color: #fff;
    border-bottom: 1px solid #c5c5c5;
    border-radius: 5px 5px 0 0;
}

.schools-container .schools-header .schools-stats {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.schools-container .schools-header .schools-stats h1 {
    font-size: 22px;
}

.schools-container .schools-header .schools-stats .officer-stats {
    width: 100%;
    padding: 0.5rem 0;
    display: flex;
    align-items: center;
}

.schools-container .schools-header .schools-stats .officer-stats p{
    font-size: 14px;
    color: #999999;
}

.schools-container .schools-header .schools-stats .officer-stats p span {
    color: #414141;
    font-weight: 500;
    font-size: 16px;
}

.schools-container .schools-body {
    width: 100%;
    display: flex;
    background-color: #fff;
    border-radius: 5px 5px 0 0;
    overflow-y: scroll;
}

.schools-container .schools-body .school-info-container {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.schools-container .schools-body .school-info-container .back-button {
    width: 50px;
}

.schools-container .schools-body .school-info-container .info-header {
    width: 100%;
    padding: 0.5rem 0;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}

.schools-container .schools-body .school-info-container .info-header h1 {
    font-size: 1.8rem;
    color: #464644;
}

.schools-container .schools-body .school-info-container .info-header button img {
    height: 20px;
}

.schools-container .schools-body .school-info-container .principal-section{
    width: 100%;
    padding: 1rem 0;
}

.schools-container .schools-body .school-info-container .principal-section h1 {
    padding: 0.5rem 0;
}