.district-officer-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.district-officer-container .district-officer-header {
    width: 100%;
    padding: 1rem;
    display: flex;
    background-color: #fff;
    border-bottom: 1px solid #c5c5c5;
    border-radius: 5px 5px 0 0;
}

.district-officer-container .district-officer-header .district-officer-stats {
    width: 80%;
    display: flex;
    flex-direction: column;
}

.district-officer-container .district-officer-header .district-officer-stats h1 {
    font-size: 22px;
}

.district-officer-container .district-officer-header .district-officer-stats .officer-stats {
    width: 100%;
    padding: 0.5rem 0;
    display: flex;
    align-items: center;
}

.district-officer-container .district-officer-header .district-officer-stats .officer-stats p{
    font-size: 14px;
    color: #999999;
}

.district-officer-container .district-officer-header .district-officer-stats .officer-stats p span {
    color: #414141;
    font-weight: 500;
    font-size: 16px;
}

.district-officer-container .district-officer-header .district-officer-add {
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
}

.district-officer-container .district-officer-header .district-officer-add .import-data {
  width: 120px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background: linear-gradient(180deg, #ffffff, #D9DBDB);
  border: 1px solid #c5c5c5;
  border-radius: 3px;
  cursor: pointer;
}

.district-officer-container .district-officer-header .district-officer-add .import-data img {
  height: 12px;
}

.district-officer-container .district-officer-header .district-officer-add .import-data p {
  font-size: 11px;
}

.district-officer-container .district-officer-header .district-officer-add .add-officer-button {
    width: 100%;
    height: 50%;
    padding: 0.5rem 1rem;
    background: linear-gradient(90deg, #3358f4, #1d8cf8);
    border-radius: 6px;
    display: flex;
    align-items: center;
    justify-content:center;
    cursor: pointer;
    box-shadow: 0px 0px 1px 0px rgba(0,0,0,0.5);
    transition: all 0.1s;
}

.district-officer-container .district-officer-header .district-officer-add .add-officer-button:hover {
    box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.75);
}

.district-officer-container .district-officer-header .district-officer-add .add-officer-button img {
    height: 20px;
    margin-right: 20px;
}

.district-officer-container .district-officer-header .district-officer-add .add-officer-button p {
    color: #fff;
}

.district-officer-container .district-officer-body {
    width: 100%;
    /* height: 100%; */
    padding: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0 0 5px 5px;
    overflow-y: scroll;
}

.district-officer-container .district-officer-body .add-officer-form {
  width: 100%;
}


.ReactTable {
    width: 100% !important;
    height: 100% !important;
    border: none !important;
  }
  
  .ReactTable .-pagination .-btn {
    background: linear-gradient(90deg, #3358f4, #1d8cf8) !important;
    color: #fff !important;
    font-weight: 600;
    border-radius: 5px !important;
  }
  
  .ReactTable .-pagination .-pageInfo {
  }
  
  .ReactTable .rt-thead .rt-resizable-header-content {
  }
  
  .ReactTable .rt-tbody .rt-tr,
  .ReactTable .rt-thead.-filters .rt-tr {
  }
  
  .ReactTable .rt-tr-group {
    height: 45px;
  }
  
  .ReactTable.-striped .rt-tr.-odd {
    background: rgba(0, 0, 0, 0.05) !important;
  }
  
  .ReactTable input,
  .ReactTable select {
    border: 1px solid rgba(0, 0, 0, 0.1);
    background: #fff;
    padding: 5px 7px;
    font-size: inherit;
    border-radius: 3px;
    font-weight: normal;
    outline: none;
  }
  
  .ReactTable input:not([type="checkbox"]):not([type="radio"]),
  .ReactTable select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }
  
  .ReactTable input:not([type="checkbox"]):not([type="radio"])::-ms-expand,
  .ReactTable select::-ms-expand {
    display: none;
  }
  
  .ReactTable .select-wrap {
    position: relative;
    display: inline-block;
  }
  
  .ReactTable .select-wrap select {
    padding: 5px 15px 5px 7px;
    min-width: 100px;
  }
  
  .ReactTable .select-wrap:after {
    content: "";
    position: absolute;
    right: 8px;
    top: 50%;
    -webkit-transform: translate(0, -50%);
    transform: translate(0, -50%);
    border-color: #999 transparent transparent;
    border-style: solid;
    border-width: 5px 5px 2.5px;
  }
  