.quiz-container {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	background-color: #f9f9f9;
}

.quiz-container .quiz-header {
	width: 100%;
	padding: 1rem;
	display: flex;
	justify-content: space-between;
	background-color: #fff;
	border-bottom: 1px solid #c5c5c5;
	border-radius: 5px 5px 0 0;
}

.quiz-container .quiz-header img {
	height: 40px;
}

.quiz-container .quiz-header button {
	width: 200px;
}

.quiz-container .quiz-header div {
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
}

.quiz-container .quiz-header h1 {
	font-size: 20px;
	font-weight: 500;
	margin: 0;
}

.quiz-container .quiz-header p {
	margin: 0 0.5rem;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: all 0.3s;
}

.quiz-container .quiz-body {
	width: 100%;
	height: 80vh;
	padding: 2rem 1rem;
	display: flex;
}
