.exam-analytics-container {
    width: 100%;
  }
  
  .exam-analytics-container .learning-breadcrumb {
    width: 100%;
    margin-top: 16vh;
    display: flex;
    align-items: center;
    padding: 1rem 3.5rem;
    background: rgb(2, 196, 215);
    background: linear-gradient(
      90deg,
      rgba(2, 196, 215, 1) 0%,
      rgba(126, 226, 186, 1) 100%
    );
  }
  
  .exam-analytics-container .learning-breadcrumb p {
    color: #fff;
    font-size: 24px;
    font-weight: 500;
    text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
  }
  
  .exam-analytics-container .learning-choose-subject-breadcrumb {
    width: 100%;
    height: 100px;
    box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.75);
    padding: 0 2rem;
    display: flex;
    align-items: center;
  }
  
  .exam-analytics-container .learning-choose-subject-breadcrumb {
    width: 100%;
    height: 100px;
    box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.75);
    padding: 0 2rem;
    display: flex;
    align-items: center;
  }
  
  .exam-analytics-container
    .learning-choose-subject-breadcrumb
    .subject-option {
    width: 180px;
    height: 90px;
    padding: 0.5rem 0;
    text-decoration: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    cursor: pointer;
    border-radius: 15px;
    transition: all 0.3s ease-in-out;
  }
  
  .exam-analytics-container
    .learning-choose-subject-breadcrumb
    .subject-option:hover {
    background-color: #f5f7fa;
  }
  
  .exam-analytics-container
    .learning-choose-subject-breadcrumb
    .subject-option
    img {
    height: 40px;
  }
  
  .exam-analytics-container
    .learning-choose-subject-breadcrumb
    .subject-option
    p {
    font-size: 14px;
    font-weight: 500;
    color: #a1a1a1;
  }
  
  .exam-analytics-container .basic-analytics {
    width: 100%;
    height: 75vh;
    padding: 2rem;
    background-color: #E9F6EF;
    background-image: url('./assets/download.png');
    background-size: contain;
    background-repeat: repeat;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .exam-analytics-container .basic-analytics .last-ten-analytics {
    width: 60%;
    height: 100%;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.15);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .exam-analytics-container .basic-analytics .last-ten-analytics h1 {
    font-size: 1.5rem;
    color: #777777;
  }
  
  .exam-analytics-container .basic-analytics .latest-analytics {
    width: 35%;
    height: 100%;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.15);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .exam-analytics-container .basic-analytics .latest-analytics h1 {
    font-size: 1.5rem;
    color: #777777;
  }
  
  .exam-analytics-container .basic-analytics .latest-analytics h5 {
    font-size: 1rem;
    color: #777777;
  }
  