.background-box {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.background-box .login-box {
	box-sizing: border-box;
	width: 100%;
	border-radius: 10px;
	height: 100%;
}

.background-box .login-box .cont-box {
	display: flex;
	flex-direction: row;
	width: 100%;
	padding: 1rem;
	padding-left: 3rem;
}

.background-box .login-box .cont-box img {
	height: 40px;
}

.background-box .login-box .text-login {
	width: 90%;
	padding-left: 8rem;
}

.background-box .login-box .text-login h5 {
	padding-top: 1rem;
	width: 43%;
}

.background-box .login-box .login-cont {
	margin-top: 16vh;
	display: flex;
	flex-direction: row;
	justify-content: center;
	background-image: linear-gradient(#205C6D, #168E79);
}

.background-box .login-box .login-cont img {
	height: 600px;
}

.background-box .login-box .login-cont .Login {
	box-sizing: border-box;
	background-color: #e4f4e6;
	width: 25%;
	border-radius: 10px;
	height: 57vh;
	padding: 1.7rem;
	margin-top: 3rem;
	/* box-shadow: 0px 0px 5px 0px rgba(156,193,248,1); */
}

.background-box .login-box .login-cont .Login p {
	text-align: center;
	padding-left: 1.5rem;
	font-size: 20px;
	font-weight: bold;
}

.background-box .login-box .login-cont .Login form {
	padding-top: 1.5rem;
}

.background-box .login-box .login-cont .Login form .about {
	display: flex;
	flex-direction: column;
	margin-top: 25px;
}

.background-box .login-box .login-cont .Login .form-group:nth-child(1) {
	margin-bottom: 1.5rem;
}

.background-box .login-box .login-cont .Login .form-group a {
	padding: 5px;
	font-size: 13px;
}

.background-box .login-box .login-cont .Login .form-group .form-control {
	background-color: transparent;
	border: 0;
	border-bottom: 2px solid #bbb;
	border-radius: 0;
	width: 98%;
}

@media all and (min-width: 480px) {
	.Login {
		padding: 60px 0;
	}

	.Login form {
		margin: 0 auto;
		max-width: 320px;
	}
}
