.footer-container {
    width: 100%;
    height: 75vh;
    background-color: #273C66;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer-container .newsletter-container {
    width: 70%;
    height: 40vh;
    background-color: #0EB582;
    border-radius: 20px;
    background-image: url('./assets/newsletter_bg.png');
    background-position: center center;
    background-size: cover;
    position: absolute;
    top: -25%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.footer-container .newsletter-container .newsletter-text{
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 3.5rem;
}

.footer-container .newsletter-container .newsletter-text h3 {
    font-size: 16px;
    color: #fff;
    font-weight: bold;
    font-family: Roboto, sans-serif;
}

.footer-container .newsletter-container .newsletter-text h1{
    color: #fff;
    font-size: 42px;
    font-weight: bold;
    font-family: Roboto, sans-serif;
	margin-bottom: 1rem;
}

.footer-container .newsletter-container .newsletter-input-button{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 3.5rem;
}

.footer-container .newsletter-container .newsletter-input-button .input-button-container{
    width: 100%;
    height: 25%;
    background-color: #fff;
    border-radius: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem;
}

.footer-container .newsletter-container .newsletter-input-button .input-button-container input {
    width: 60%;
    height: 100%;
    border: none;
    outline: none;
}

.footer-container .newsletter-container .newsletter-input-button .input-button-container button{
    width: 40%;
    height: 100%;
    border-radius: 40px;
    background-color: #273C66;
    outline: none;
    border: none;
    color: #fff;
    font-size: 14px;
}

.footer-container .footer-links {
    width: 70%;
    height: 30vh;
    position: absolute;
    top: 30%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.footer-container .footer-links .links-column {
    width: 23%;
    height: 100%;
    padding: 3rem 0;
    display: flex;
    flex-direction: column;
}

.footer-container .footer-links .links-column h1 {
    font-size: 18px;
    color: #fff;
    font-weight: bold;
    font-family: Roboto, sans-serif;
    margin-bottom: 1rem;
}

.footer-container .footer-links .links-column img {
    height: 30px;
    width: 80px;
    margin-bottom: 1rem;
}

.footer-container .footer-links .links-column p {
    color: #a5b7d9;
    margin-bottom: 0.6rem;
}

.footer-container .footer-links .links-column span {
    color: #a5b7d9;
    font-size: 13px;
}

.footer-container .footer-copyright {
    width: 70%;
    height: 15vh;
    border-top: 1px solid #a5b7d9;
    position: absolute;
    bottom: 0%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.footer-container .footer-copyright p{
    color: #fff;
}