.reply-box-container {
    width: 100%;
    background-color: #fff;
    border: 1px solid #DCECF3;
    border-radius: 5px;
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
}

.reply-box-container .reply-owner-info {
    width: 100%;
    padding: 0.3rem 0;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.reply-box-container .reply-owner-info img {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.reply-box-container .reply-owner-info div p:last-child{
    font-size: 13px;
    color: #a1a1a1;
}