.scores-container {
    width: 80%;
    padding: 2rem 0;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.15);
    display: flex;
    justify-content: center;
}

.scores-container .scores-board{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.scores-container .scores-board .scores-head {
    width: 100%;
    padding: 0 1rem;
    display: flex;
    align-items: center;
}

.scores-container .scores-board .scores-head h1 {
    font-size: 1.5rem;
    color: #555555;
    margin: 0;
}

.scores-container .scores-board .scores-body {
    width: 100%;
    padding:1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.scores-container .scores-board .scores-body .scores-table-head {
    width: 100%;
    height: 65px;
    background-color: #00AEEF;
    display: flex;
}

.scores-container .scores-board .scores-body .scores-table-head .table-head-left {
    width: 50%;
    height: 100%;
    padding: 0 0.5rem;
    display: flex;
    align-items: center;
}

.scores-container .scores-board .scores-body .scores-table-head .table-head-left p {
    color: #fff;
    font-weight: 500;
    font-size: 13px; 
}

.scores-container .scores-board .scores-body .scores-table-head .table-head-right {
    width: 50%;
    height: 100%;
    padding: 0 0.5rem;
    display: flex;
    align-items: center;
}

.scores-container .scores-board .scores-body .scores-table-head .table-head-right .score{
    width: 20%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-right: 1px solid #d5d5d5;
    border-left: 1px solid #d5d5d5;
}

.scores-container .scores-board .scores-body .scores-table-head .table-head-right .score p {
    color: #fff;
    font-weight: 500;
    font-size: 13px;   
}

.scores-container .scores-board .scores-body .scores-table-head .table-head-right .questions{
    width: 20%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.scores-container .scores-board .scores-body .scores-table-head .table-head-right .questions p {
    color: #fff;
    font-weight: 500;  
    font-size: 13px;  
}

.scores-container .scores-board .scores-body .scores-table-head .table-head-right .time-taken{
    width: 25%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.scores-container .scores-board .scores-body .scores-table-head .table-head-right .time-taken p {
    color: #fff;
    font-weight: 500;  
    font-size: 13px; 
}

.scores-container .scores-board .scores-body .scores-table-head .table-head-right .last-practiced{
    width: 35%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.scores-container .scores-board .scores-body .scores-table-head .table-head-right .last-practiced p {
    color: #fff;
    font-weight: 500;  
    font-size: 13px; 
}

.scores-container .scores-board .scores-body .scores-table-body {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.scores-container .scores-board .scores-body .scores-table-body .scores-accordion {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 1px;
}

.scores-container .scores-board .scores-body .scores-table-body .scores-accordion .accordion {
    background-color: #eee;
    color: #444;
    cursor: pointer;
    padding: 1rem 0.5rem;
    width: 100%;
    border: none;
    text-align: left;
    outline: none;
    font-size: 15px;
    transition: all 0.4s ease-in-out;
    display: flex;
    align-items: center;
}

.scores-container .scores-board .scores-body .scores-table-body .scores-accordion .accordion h1{
    font-size: 1rem;
    color: #5b8a9b;
}

.scores-container .scores-board .scores-body .scores-table-body .scores-accordion .accordion p{
    font-size: 13px;
    color: #5b8a9b;
}

.scores-container .scores-board .scores-body .scores-table-body .scores-accordion .panel {
    display: none;
    background-color: white;
    overflow: hidden;
    flex-direction: column;
}

.scores-container .scores-board .scores-body .scores-table-body .scores-accordion .panel .stat-panel {
    width: 100%;
    display: flex;
    align-items: center;
}

.scores-container .scores-board .scores-body .scores-table-body .scores-accordion .panel .stat-panel:hover {
    background-color: 	rgba(255,255,102,0.35);
}

.scores-container .scores-board .scores-body .scores-table-body .scores-accordion .panel .stat-panel .panel-left {
    width: 50%;
    height: 40px;
    display: flex;
    align-items: center;
    padding: 0rem 1.5rem;
}

.scores-container .scores-board .scores-body .scores-table-body .scores-accordion .panel .stat-panel .panel-left p {
    font-size: 13px;
    color: #616161;
}

.scores-container .scores-board .scores-body .scores-table-body .scores-accordion .panel .stat-panel .panel-right {
    width: 50%;
    height: 40px;
    padding: 0 0 0 0.5rem;
    display: flex;
    align-items: center;
}

.scores-container .scores-board .scores-body .scores-table-body .scores-accordion .panel .stat-panel .panel-right .score{
    width: 20%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-left: 1px solid #d5d5d5;
    border-right: 1px solid #d5d5d5;
    border-bottom: 1px solid #d5d5d5;
}

.scores-container .scores-board .scores-body .scores-table-body .scores-accordion .panel .stat-panel .panel-right .score p {
    font-size: 13px; 
    color: #616161;  
}

.scores-container .scores-board .scores-body .scores-table-body .scores-accordion .panel .stat-panel .panel-right .questions{
    width: 20%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid #d5d5d5;
    background-color: #fafafa;
}

.scores-container .scores-board .scores-body .scores-table-body .scores-accordion .panel .stat-panel .panel-right .questions p {

    font-size: 13px; 
    color: #616161;  
}

.scores-container .scores-board .scores-body .scores-table-body .scores-accordion .panel .stat-panel .panel-right .time-taken{
    width: 25%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid #d5d5d5;
    background-color: #fafafa;
}

.scores-container .scores-board .scores-body .scores-table-body .scores-accordion .panel .stat-panel .panel-right .time-taken p {
    font-size: 13px; 
    color: #616161;
}

.scores-container .scores-board .scores-body .scores-table-body .scores-accordion .panel .stat-panel .panel-right .last-practiced{
    width: 35%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid #d5d5d5;
    background-color: #fafafa;
}

.scores-container .scores-board .scores-body .scores-table-body .scores-accordion .panel .stat-panel .panel-right .last-practiced p {
    font-size: 13px; 
    color: #616161;
}

.horizontal-slider {
    color: red;
}

.example-thumb {
    height: 10px;
    width: 10px;
    background-color: green;
    border-radius: 50%;
}
  