.usage-teacher-container {
    width: 100%;
    padding: 2rem 0;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.15);
    display: flex;
    justify-content: center;
    margin: 1rem auto;
}

.usage-teacher-container .usage-board {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.usage-teacher-container .usage-board .usage-head {
    width: 100%;
    padding: 0 1rem;
    display: flex;
    align-items: center;
}

.usage-teacher-container .usage-board .usage-head h1 {
    font-size: 1.5rem;
    color: #555555;
    margin: 0;
}

.usage-teacher-container .usage-board .usage-stats {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 2rem;
}

.usage-teacher-container .usage-board .usage-stats .stats-box {
    width: 33%;
    height: 100%;
    display: flex;
    border-right: 1px solid #c5c5c5;
}

.usage-teacher-container .usage-board .usage-stats .stats-box:last-child {
    border: none;
}

.usage-teacher-container .usage-board .usage-stats .stats-box .left {
    width: 45%;
    display: flex;
    justify-content: flex-end;
    padding: 0.5rem 1rem;
}

.usage-teacher-container .usage-board .usage-stats .stats-box .left #pencil {
    color: #52B700;
}

.usage-teacher-container .usage-board .usage-stats .stats-box .left #clock {
    color: #26A0FC;
}

.usage-teacher-container .usage-board .usage-stats .stats-box .left #puzzle {
    color: #E77339;
}

.usage-teacher-container .usage-board .usage-stats .stats-box .left p {
    font-size: 2.5rem;
}

.usage-teacher-container .usage-board .usage-stats .stats-box .right {
    width: 55%;
    display: flex;
    flex-direction: column;
    padding:1rem;
}

.usage-teacher-container .usage-board .usage-stats .stats-box .right p {
    font-size: 14px;
    color: #666666;
    font-weight: 500;
}

.usage-teacher-container .usage-board .usage-stats .stats-box .right h1 {
    color: #666666;
}

.usage-teacher-container .usage-charts{
    width: 100%;
    height: 100%;
    display: flex;
}

.usage-teacher-container .usage-charts .doughnut-chart {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.usage-teacher-container .usage-charts .bar-chart {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}