.sub-analytics-container {
    width: 100%;
}

.sub-analytics-container .learning-breadcrumb {
    width: 100%;
    margin-top: 16vh;
    display: flex;
    align-items: center;
    padding: 1rem 3.5rem;
    background: rgb(2,196,215);
    background: linear-gradient(90deg, rgba(2,196,215,1) 0%, rgba(126,226,186,1) 100%);
}

.sub-analytics-container .learning-breadcrumb p {
    color:#fff;
    font-size: 24px;
    font-weight: 500;
    text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.2);
}

.sub-analytics-container  .learning-choose-subject-breadcrumb {
    width: 100%;
    height: 100px;
    z-index: 999;
    box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.15);
    padding: 0 2rem;
    display: flex;
    align-items: center;
}

.sub-analytics-container  .learning-choose-subject-breadcrumb .subject-option {
    width: 180px;
    height: 90px;
    padding: 0.5rem 0;
    text-decoration: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    cursor: pointer;
    border-radius: 15px;
    transition: all 0.3s ease-in-out;
}

.sub-analytics-container  .learning-choose-subject-breadcrumb .subject-option:hover {
    background-color: #f5f7fa;
}

.sub-analytics-container  .learning-choose-subject-breadcrumb .subject-option img {
    height: 40px;
}

.sub-analytics-container  .learning-choose-subject-breadcrumb .subject-option p {
    font-size: 14px;
    font-weight: 500;
    color: #a1a1a1;
}

.sub-analytics-container .select-analytics-type {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    /* padding: 1rem 0; */
    background-color: #f5f7fa;
    border: 1px solid #c5c5c5;
}

.sub-analytics-container .select-analytics-type .tab-type {
    width: 15%;
    border-right: 1px solid #c5c5c5;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}

.sub-analytics-container .select-analytics-type .tab-type:last-child {
    border: none;
}

.sub-analytics-container .select-analytics-type .tab-type h1 {
    font-size: 1.2rem;
    color: #717171;
}

.sub-analytics-container .select-analytics-type .tab-type p {
    font-size: 12px;
    color: #717171;
}

.sub-analytics-container .select-analytics-type .tab-type:hover h1,
.sub-analytics-container .select-analytics-type .tab-type:hover p {
    color: #26A0FC;
}

.sub-analytics-container .select-analytics-type .tab-type.active {
    border-bottom: 5px solid #26A0FC;
}

.sub-analytics-container .select-analytics-type .tab-type.active h1,
.sub-analytics-container .select-analytics-type .tab-type.active p {
    color: #26A0FC;
}

.sub-analytics-container .report-whiteboard {
    width: 100%;
    padding: 2rem;
    background-color: #E9F6EF;
    background-image: url('../assets/download.png');
    background-size: contain;
    background-repeat: repeat;
    display: flex;
    justify-content: center;
}