.message-box-container {
    width: 80%;
    background-color: #fff;
    border: 1px solid #DCECF3;
    border-radius: 5px;
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
}

.message-box-container .msg-owner-info {
    width: 100%;
    padding: 0.3rem 0;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.message-box-container .msg-owner-info img {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.message-box-container .msg-owner-info div p:last-child{
    font-size: 13px;
    color: #a1a1a1;
}

.message-box-container .msg-total-reply{
    margin-top: 10px;
    width: 100%;
    display: flex;
    align-items: center;
}

.message-box-container .msg-total-reply p {
    color: #a1a1a1;
    font-size: 14px;
    cursor: pointer;
}

.message-box-container .msg-total-reply span {
    font-size: 14px;
    margin-left: 10px;
    color: #3c8dbc;
    cursor: pointer;
    text-decoration: underline;
}

.message-box-container .msg-reply-section {
    width: 100%;
    /* margin-top: 10px; */
    display: none;
    transition: all 0.3s ease-in-out;
}

.message-box-container .msg-reply-section.active {
    padding: 1rem;
    display: flex;
    flex-direction: column;
}