.teachers-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.teachers-container .teachers-header {
    width: 100%;
    padding: 1rem;
    display: flex;
    background-color: #fff;
    border-bottom: 1px solid #c5c5c5;
    border-radius: 5px 5px 0 0;
}

.teachers-container .teachers-header .teachers-stats {
    width: 80%;
    display: flex;
    flex-direction: column;
}

.teachers-container .teachers-header .teachers-stats h1 {
    font-size: 22px;
}

.teachers-container .teachers-header .teachers-stats .officer-stats {
    width: 100%;
    padding: 0.5rem 0;
    display: flex;
    align-items: center;
}

.teachers-container .teachers-header .teachers-stats .officer-stats p{
    font-size: 14px;
    color: #999999;
}

.teachers-container .teachers-header .teachers-stats .officer-stats p span {
    color: #414141;
    font-weight: 500;
    font-size: 16px;
}

.teachers-container .teachers-header .teachers-add {
    width: 20%;
    display: flex;
    justify-content: center;
}

.teachers-container .teachers-header .teachers-add .add-officer-button {
    width: 80%;
    height: 50%;
    padding: 0.5rem 1rem;
    background: linear-gradient(90deg, #3358f4, #1d8cf8);
    border-radius: 6px;
    display: flex;
    align-items: center;
    justify-content:center;
    cursor: pointer;
    box-shadow: 0px 0px 1px 0px rgba(0,0,0,0.5);
    transition: all 0.1s;
}

.teachers-container .teachers-header .teachers-add .add-officer-button:hover {
    box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.75);
}

.teachers-container .teachers-header .teachers-add .add-officer-button img {
    height: 20px;
    margin-right: 20px;
}

.teachers-container .teachers-header .teachers-add .add-officer-button p {
    color: #fff;
}

.teachers-container .teachers-body {
    width: 100%;
    /* height: 100%; */
    padding: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0 0 5px 5px;
    overflow-y: scroll;
}

.container-group{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.image-container{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.image-container p{
    margin-right: 1rem;
}

.image-container .image-picker{
    width: 150px;
    height: 150px;
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #e5e5e5;
    border-radius: 50%;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.15);
}

.image-container .image-picker input{
    width: 100%;
    height: 100%;
    opacity: 0;
    position: absolute;
    top: 0;
    cursor: pointer;
}

.image-container img{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
    cursor: pointer;
}

.image-container .image-picker .image-icon{
    width: 50px;
    height: 50px;
    border-radius: 0;
}

.btn-primary{
    background: linear-gradient(90deg, #3358f4, #1d8cf8);
}

.success-text{
    font-size: 1rem;
    color: #218838;
}

.danger-text{
    font-size: 1rem;
    color: #de3700;
}

.action-buttons{
    width: 50px !important;
    height: 25px !important;
    font-size: 12px !important;
    margin: 0 0.25rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.tab-action-button{
    width: 100% !important;
    height: 35px !important;
    font-size: 14px !important;
}

.submit-action{
    height: 35px !important;
    font-size: 14px !important;
    display: flex;
    align-items: center;
    justify-content: center;
    margin:0.25rem
}

svg{
    margin:0 0.25rem
}

.modal-header .close{
    outline: none;
}

.school-info-list{
    width: 100%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}