.my-exams-container {
	width: 100%;
	min-height: 80vh;
	display: flex;
	align-items: center;
	justify-content: center;
}

.my-exams-container.add-padding {
    padding: 3.5rem 0;
}

.my-exams-container .my-exams-wrapper {
	width: 50%;
	padding: 1rem;
	background-color: #f5f7fa;
	border-radius: 6px;
	box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.1);
	display: flex;
	flex-direction: column;
	align-items: center;
}

.my-exams-container .my-exams-wrapper .select-sub {
	width: 100%;
	display: flex;
	align-items: center;
	margin-bottom: 2rem;
}

.my-exams-container .my-exams-wrapper .exam-list {
	width: 100%;
	padding: 0 1rem;
	display: flex;
	flex-direction: column;
	align-items: center;
}
