.single-exam-container {
	width: 100%;
	border-radius: 6px;
	border: 1px solid #c5c5c5;
	background-color: #fff;
	margin-bottom: 1rem;
	padding: 0 0.5rem;
	display: flex;
	align-items: center;
	justify-content: center;
}

.single-exam-container img {
	height: 50px;
	width: 50px;
}

.single-exam-container .exam-head {
	width: 100%;
	padding: 1rem;
	display: flex;
	flex-direction: column;
}

.single-exam-container .exam-head p {
	font-size: 16px;
	font-weight: bold;
	color: #464c4f;
	margin-bottom: 0.5rem;
}

.single-exam-container .exam-head .objectives {
	font-weight: normal;
	font-size: 14px;
}

.single-exam-container .exam-head h5 {
	font-weight: normal;
	font-size: 14px;
}

.single-exam-container .exam-head span {
	font-size: 12px;
	font-weight: bold;
	color: #717171;
}

.single-exam-container .exam-head button {
	/* width: 30%; */
	margin: 1rem 0;
	background: #fff;
	background-image: linear-gradient(-180deg, #fff 0, #efefef);
	border-radius: 3px;
	border: 1px solid #d7dbdb;
	box-shadow: inset 0 -1px 0 0 #d7dbdb;
	color: #464c4f;
    padding: 0.5rem 0.8rem;
    /* min-height: 50px; */
	font-size: 12px;
	outline: none;
	transition: all 0.3s ease-in-out;
}

.single-exam-container .exam-head button .timer-text {
    margin-right: 10px;
}
