.my-seating-container {
    width: 100%;
    min-height: 80vh;
    /* padding: 1rem 0; */
    padding: 3.5rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.my-seating-container .my-seating-wrapper {
    width: 50%;
    padding: 1rem 0;
    background-color: #f5f7fa;
    border-radius: 6px;
    box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.my-seating-container .my-seating-wrapper .select-sub{
    width: 100%;
    display: flex;
    align-items: center;
}

.my-seating-container .my-seating-wrapper .seating-chart{
    width: 100%;
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
