.parent-stats-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.parent-stats-container  .select-analytics-type {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f5f7fa;
    border: 1px solid #c5c5c5;
}

.parent-stats-container  .select-analytics-type .tab-type {
    width: 15%;
    padding: 0.1rem 0;
    border-right: 1px solid #c5c5c5;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}

.parent-stats-container  .select-analytics-type .tab-type:last-child {
    border: none;
}

.parent-stats-container  .select-analytics-type .tab-type h1 {
    font-size: 1.2rem;
    color: #717171;
}

.parent-stats-container  .select-analytics-type .tab-type p {
    font-size: 12px;
    color: #717171;
}

.parent-stats-container  .select-analytics-type .tab-type:hover h1,
.parent-stats-container  .select-analytics-type .tab-type:hover p {
    color: #26A0FC;
}

.parent-stats-container  .select-analytics-type .tab-type.active {
    border-bottom: 5px solid #26A0FC;
}

.parent-stats-container  .select-analytics-type .tab-type.active h1,
.parent-stats-container  .select-analytics-type .tab-type.active p {
    color: #26A0FC;
}

.parent-stats-container .report-whiteboard {
    width: 100%;
    padding: 2rem;
    background-color: #E9F6EF;
    display: flex;
    justify-content: center;
}

.sub-loading-container{
    width: 100%;
    height: 60vh;
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: red; */
}