.top-nav-container{
    width: 100%;
    height: 6vh;
    padding: 0 3.5rem;
    position: fixed;
    top: 0;
    z-index: 99;
    background-color: #1E2F50;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.top-nav-container .nav-social-icons{
    display: flex;
    align-items: center;
}

.top-nav-container .nav-social-icons p {
    color: #fff;
    font-size: 16px;
    margin-right: 10px;
    cursor: pointer;
}

.top-nav-container .nav-social-icons p:nth-child(1){
    transition: all 0.3s ease-in-out;
}

.top-nav-container .nav-social-icons p:nth-child(1):hover {
    transform: scale(0.95);
    transition: all 0.3s ease-in-out;
    color: #3b5998;
}

.top-nav-container .nav-social-icons p:nth-child(2){
    transition: all 0.3s ease-in-out;
}

.top-nav-container .nav-social-icons p:nth-child(2):hover {
    transform: scale(0.95);
    transition: all 0.3s ease-in-out;
    color: #00acee;
}

.top-nav-container .nav-social-icons p:nth-child(3){
    transition: all 0.3s ease-in-out;
}

.top-nav-container .nav-social-icons p:nth-child(3):hover {
    transform: scale(0.95);
    transition: all 0.3s ease-in-out;
    color:#8a3ab9;
}

.top-nav-container .nav-social-icons p:nth-child(4){
    transition: all 0.3s ease-in-out;
}

.top-nav-container .nav-social-icons p:nth-child(4):hover {
    transform: scale(0.95);
    transition: all 0.3s ease-in-out;
    color:#0e76a8;
}

.top-nav-container .nav-email-info{
    display: flex;
    align-items: center;
}

.top-nav-container .nav-email-info p {
    color: #fff;
    font-size: 13px;
    margin-right: 5px;
    cursor: pointer;
}

.top-nav-container .nav-email-info p:first-child{
    transition: all 0.3s ease-in-out;
}

.top-nav-container .nav-email-info p:first-child:hover {
    transform: scale(0.8);
    transition: all 0.3s ease-in-out;
}