.quiz-container {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	background-color: #f9f9f9;
}

.quiz-container .quiz-header {
	width: 100%;
	padding: 1rem;
	display: flex;
	justify-content: space-between;
	background-color: #fff;
	border-bottom: 1px solid #c5c5c5;
	border-radius: 5px 5px 0 0;
}

.quiz-container .quiz-header img {
	height: 40px;
}

.quiz-container .quiz-header button {
	width: 200px;
}

.quiz-container .quiz-header div {
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
}

.quiz-container .quiz-header h1 {
	font-size: 20px;
	font-weight: 500;
	margin: 0;
}

.quiz-container .quiz-header p {
	margin: 0 0.5rem;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: all 0.3s;
}

.quiz-container .quiz-body {
	width: 100%;
	height: 80vh;
	padding: 2rem 1rem;
	display: flex;
}

.student-list {
	width: 100%;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}

.student-list .student-box {
	width: 260px;
	height: 320px;
	display: flex;
	flex-direction: column;
	background-color: #0041c2;
	box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.25);
	justify-content: flex-end;
}

.student-list .student-box .student-box-body {
	width: 100%;
	height: 240px;
	background-color: #fff;
	display: flex;
	flex-direction: column;
	position: relative;
	align-items: center;
	padding-top: 60px;
}

.student-list .student-box .student-box-body img {
	position: absolute;
	width: 80px;
	height: 80px;
	border-radius: 50%;
	top: -40px;
	overflow: hidden;
	object-fit: cover;
}

.student-list .student-box .student-box-body h1 {
	color: #0041c2;
	font-size: 1.25rem;
}

.student-list .student-box .student-box-body p {
	font-size: 1rem;
	color: #464646;
	margin: 0.5rem 0;
}

.student-list .student-box .student-box-body button {
	position: absolute;
	right: 10px;
	bottom: 10px;
	width: 40px;
	height: 40px;
	border-radius: 50%;
	border: 2px solid #0041c2;
	background-color: transparent;
	display: flex;
	align-items: center;
	justify-content: center;
	outline: none;
}

.student-list .student-box .student-box-body button i {
	color: #0041c2;
}

.note-text-marks {
	display: flex;
	margin: 0.25rem 0;
	justify-content: space-between;
}

.note-text-marks h1 {
	font-size: 1.5rem;
}

.note-text-marks input {
	width: 70%;
	height: 40px;
	border-radius: 5px;
	border: 1px solid #464646;
	padding: 0 0.5rem;
	font-size: 1rem;
}
