.recognition-container{
    width: 100%;
    height: 100%;
    display: flex;
}

.recognition-container .recognised-wrap {
    width: 100%;
    height: 100%;
    position: relative;
    padding: 1rem;
    display: flex;
}

.recognition-container .recognised-wrap .most-recognised{
    width: 40%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.recognition-container .recognised-wrap .most-recognised h1 {
    font-size: 1.5rem;
}

.recognition-container .recognised-wrap .others {
    width: 60%;
    height: 100%;
    box-shadow:0px 0px 5px 0px rgba(0,0,0,0.15);
    border-radius: 10px;
    display: flex;
    flex-wrap: wrap;
    padding: 1rem;
    overflow-y: scroll;
}

