.student-learning-container {
  width: 100%;
}

.student-learning-container .learning-breadcrumb {
  width: 100%;
  margin-top: 16vh;
  display: flex;
  align-items: center;
  padding: 1rem 3.5rem;
  background: rgb(2, 196, 215);
  background: linear-gradient(
    90deg,
    rgba(2, 196, 215, 1) 0%,
    rgba(126, 226, 186, 1) 100%
  );
}

.student-learning-container .learning-breadcrumb p {
  color: #fff;
  font-size: 24px;
  font-weight: 500;
  text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.2);
}

.student-learning-container .learning-choose-subject-breadcrumb {
  width: 100%;
  height: 100px;
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.75);
  padding: 0 2rem;
  display: flex;
  align-items: center;
}

.student-learning-container
  .learning-choose-subject-breadcrumb
  .subject-option {
  width: 180px;
  height: 90px;
  padding: 0.5rem 0;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  cursor: pointer;
  border-radius: 15px;
  transition: all 0.3s ease-in-out;
}

.student-learning-container
  .learning-choose-subject-breadcrumb
  .subject-option:hover {
  background-color: #f5f7fa;
}

.student-learning-container
  .learning-choose-subject-breadcrumb
  .subject-option
  img {
  height: 40px;
}

.student-learning-container
  .learning-choose-subject-breadcrumb
  .subject-option
  p {
  font-size: 14px;
  font-weight: 500;
  color: #a1a1a1;
}

.student-learning-container .learning-grade-breadcrumb {
  width: 100%;
  background: #e9f6ef;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.15);
  display: flex;
  align-items: center;
  padding: 1rem 3.5rem;
}

.student-learning-container .learning-grade-breadcrumb p {
  color: #00b255;
  font-size: 18px;
  font-weight: bold;
  text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1);
}

.student-learning-container .learning-chapter-list {
  width: 100%;
    background-color: #E9F6EF;
    background-image: url('./assets/download.png');
    background-size: contain;
    background-repeat: repeat;
  /* background-image: url("./assets/course_bg-2.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top; */
  display: flex;
  flex-wrap: wrap;
  padding: 3.5rem 3.5rem;
}

.student-learning-container .learning-chapter-list .chapter-card {
  width: 30%;
  margin: 0 1rem 2.5rem 1rem;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.25);
}

.student-learning-container .learning-chapter-list .chapter-card .chapter-head {
  width: 100%;
  padding: 0.7rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px 10px 0 0;
  background: rgb(2, 196, 215);
  background: linear-gradient(
    90deg,
    rgba(2, 196, 215, 1) 0%,
    rgba(126, 226, 186, 1) 100%
  );
}

.student-learning-container
  .learning-chapter-list
  .chapter-card
  .chapter-head
  p {
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1);
}

.student-learning-container
  .learning-chapter-list
  .chapter-card
  .chapter-topics {
  width: 100%;
  height: 200px;
  background-color: #fff;
  border-radius: 0 0 10px 10px;
  padding: 1rem;
}

.student-learning-container
  .learning-chapter-list
  .chapter-card
  .chapter-topics
  .topic-menu {
  width: 100%;
  display: flex;
  margin-bottom: 10px;
}

.student-learning-container
  .learning-chapter-list
  .chapter-card
  .chapter-topics
  .topic-menu
  .menu-left {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
}

.student-learning-container
  .learning-chapter-list
  .chapter-card
  .chapter-topics
  .topic-menu
  .menu-left
  p {
  font-size: 14px;
}

.student-learning-container
  .learning-chapter-list
  .chapter-card
  .chapter-topics
  .topic-menu
  .menu-right {
  width: 30%;
  display: flex;
  align-items: center;
}

.student-learning-container
  .learning-chapter-list
  .chapter-card
  .chapter-topics
  .topic-menu
  .menu-right
  p {
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
  color: purple;
}

.student-learning-container .modal-overlay-container {
  width: 100%;
  height: 100vh;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.35);
  top: 0;
  left: 0;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
}

.student-learning-container .modal-overlay-container .modal-box {
  width: 50vw;

  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.45);
  display: flex;
  flex-direction: column;
}

.student-learning-container .modal-overlay-container .modal-box .modal-head {
  width: 100%;
  padding: 1rem;
  border-radius: 10px 10px 0 0;
  box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.15);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.student-learning-container .modal-overlay-container .modal-box .modal-head p {
  font-size: 24px;
  font-weight: bold;
  color: #00b255;
}

.student-learning-container
  .modal-overlay-container
  .modal-box
  .modal-head
  img {
  height: 30px;
  cursor: pointer;
}

.student-learning-container .modal-overlay-container .modal-box .modal-body {
  width: 100%;
  max-height: 80vh;
  padding: 2rem 5rem;
  border-radius: 0 0 10px 10px;
  background-image: url("../assets/mosac.jpg");
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  overflow-y: scroll;
}

.student-learning-container
  .modal-overlay-container
  .modal-box
  .modal-body
  .modal-option {
  width: 100%;
  padding: 1rem;
  margin: 1rem 0;
  border-radius: 10px;
  cursor: pointer;
  background-color: #fff;
  box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.15);
  display: flex;
  align-items: center;
  text-decoration: none;
}

.student-learning-container
  .modal-overlay-container
  .modal-box
  .modal-body
  .modal-option
  div {
  width: 40%;
  margin-right: 20px;
  display: flex;
  justify-content: flex-end;
}

.student-learning-container
  .modal-overlay-container
  .modal-box
  .modal-body
  .modal-option
  div
  img {
  height: 40px;
}

.student-learning-container
  .modal-overlay-container
  .modal-box
  .modal-body
  .modal-option
  p {
  font-size: 18px;
  font-weight: 500;
  color: #00b255;
}
