
.students-container .students-header {
	width: 100%;
	padding: 1rem;
	display: flex;
	background-color: #fff;
	border-bottom: 1px solid #c5c5c5;
    border-radius: 5px 5px 0 0;
}

.students-container .students-header .students-stats {
	width: 80%;
	display: flex;
	flex-direction: column;
}

.students-container .students-header .students-stats h1 {
	font-size: 22px;
}

.students-container .students-header .students-stats .officer-stats {
	width: 100%;
	padding: 0.5rem 0;
	display: flex;
	align-items: center;
}

.students-container .students-header .students-stats .officer-stats p {
	font-size: 14px;
	color: #999999;
}

.students-container .students-header .students-stats .officer-stats p span {
	color: #414141;
	font-weight: 500;
	font-size: 16px;
}

.students-container .students-header .students-add {
	width: 20%;
	display: flex;
	justify-content: center;
}

.students-container .students-header .students-add .add-officer-button {
	width: 80%;
	height: 50%;
	padding: 0.5rem 1rem;
	background: linear-gradient(90deg, #3358f4, #1d8cf8);
	border-radius: 6px;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.5);
	transition: all 0.1s;
}

.students-container .students-header .students-add .add-officer-button:hover {
	box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.75);
}

.students-container .students-header .students-add .add-officer-button img {
	height: 20px;
	margin-right: 20px;
}

.students-container .students-header .students-add .add-officer-button p {
	color: #fff;
}

.attendance-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
}


.attendance-container .attendance-list{
    width: 60%;
}

.attendance-container .attendance-calender{
    width: 40%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.attendance-container .attendance-action{
    border-top: 2px solid #1e8af8;
    border-bottom: 2px solid #1e8af8;
    border-left: 2px solid #1e8af8;
    padding: 0rem 0.8rem;
    color: #1e8af8;
    cursor: pointer;
}

.attendance-container .attendance-action:nth-child(1){
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
}

.attendance-container .attendance-action:nth-child(2){
    border-right: 2px solid #1e8af8;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
}

.attendance-container .attendance-action.active:nth-child(2) {
    background-color: #28A745;
    border-color: #28A745;
    color: #fff;
}

.attendance-container .attendance-action.active:nth-child(1) {
    background-color: #de3700;
    border-color: #de3700;
    color: #fff;
}