.parent-homepage-container {
    width: 100%;
}

.parent-homepage-container .iigp-navbar-container {
    width: 100%;
    height: 8vh;
    padding: 0 3.5rem;
    position: fixed;
    top: 0;
    z-index: 999;
    box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.15);
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
}

.parent-homepage-container .iigp-navbar-container .iigp-logo {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
}

.parent-homepage-container .iigp-navbar-container .iigp-logo img {
    height: 40px;
}

.parent-homepage-container .iigp-navbar-container .menu-links {
    width: 60%;
    height: 100%;
    display: flex;
    align-items: center;
}

.parent-homepage-container .iigp-navbar-container .menu-links .link-box {
    width: 20%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
}

.parent-homepage-container .iigp-navbar-container .menu-links .link-box::before {
    content: '';
    position: absolute;
    width: 0;
    bottom: -5px;
    height: 4px;
    background-color:green;
}

.parent-homepage-container .iigp-navbar-container .menu-links .link-box:hover::before {
    content: '';
    position: absolute;
    width: 70px;
    bottom: 0px;
    height: 4px;
    background-color:#00B255;
    border-radius: 3px;
    transition: width 0.5s ease-in-out;
    transform-origin: left;
}

.parent-homepage-container .iigp-navbar-container .menu-links .link-box p {
    color: #00B255;
    font-weight: 500;
    cursor: pointer;
}

.parent-homepage-container .iigp-navbar-container .menu-links .link-user {
    width: 40%;
    padding: 0 1rem;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    cursor: pointer;
}

.parent-homepage-container .iigp-navbar-container .menu-links .link-user #user-icon {
    height: 40px;
    width: 40px;
    object-fit: cover;
    border-radius: 50%;
}

.parent-homepage-container .iigp-navbar-container .menu-links .link-user #arrow-icon {
    height: 12px;
}

.parent-homepage-container .learning-breadcrumb {
    width: 100%;
    margin-top: 8vh;
    display: flex;
    align-items: center;
    padding: 1rem 3.5rem;
    background: rgb(2,196,215);
    background: linear-gradient(90deg, rgba(2,196,215,1) 0%, rgba(126,226,186,1) 100%);
}

.parent-homepage-container .learning-breadcrumb p {
    color:#fff;
    font-size: 24px;
    font-weight: bold;
    text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
}

.parent-homepage-container .learning-choose-subject-breadcrumb {
    width: 100%;
    height: 100px;
    box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.75);
    padding: 0 2rem;
    display: flex;
    align-items: center;
}

.parent-homepage-container .learning-choose-subject-breadcrumb .subject-option {
    width: 180px;
    height: 90px;
    padding: 0.5rem 0;
    text-decoration: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    cursor: pointer;
    border-radius: 15px;
    transition: all 0.3s ease-in-out;
}

.parent-homepage-container .learning-choose-subject-breadcrumb .subject-option:hover {
    background-color: #f5f7fa;
}

.parent-homepage-container .learning-choose-subject-breadcrumb .subject-option img {
    height: 40px;
}

.parent-homepage-container .learning-choose-subject-breadcrumb .subject-option p {
    font-size: 14px;
    font-weight: 500;
    color: #a1a1a1;
}

.parent-homepage-container .basic-analytics {
    width: 100%;
    height: 70vh;
    padding: 2rem;
    background: #E9F6EF;
    display: flex;align-items: center;
    justify-content: space-between;
}

.parent-homepage-container .basic-analytics .last-ten-analytics {
    width: 60%;
    height: 100%;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.15);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.parent-homepage-container .basic-analytics .last-ten-analytics h1 {
    font-size: 1.5rem;
    color: #777777;
}

.parent-homepage-container .basic-analytics .latest-analytics {
    width: 35%;
    height: 100%;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.15);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.parent-homepage-container .basic-analytics .latest-analytics h1 {
    font-size: 1.5rem;
    color: #777777;
}

.parent-homepage-container .basic-analytics .latest-analytics h5 {
    font-size: 1rem;
    color: #777777;
}

/* Select child modal */

.child-select-overlay {
    width: 100%;
    height: 100vh;
    background: url('./assets/discover-blue-bg-1920-1x.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
}

.select-child-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.select-child-wrapper h1 {
    font-size: 24px;
    color: #52B700;
}

.select-child-wrapper .children-list-container {
    width: 100%;
    padding: 1rem 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}