.scores-teacher-container {
    width: 90%;
    padding: 2rem 0;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.15);
    display: flex;
    justify-content: center;
    margin: 1rem auto;
}

.scores-teacher-container .scores-teacher-board{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.scores-teacher-container .scores-teacher-board .scores-teacher-head {
    width: 100%;
    padding: 0 1rem;
    display: flex;
    align-items: center;
}

.scores-teacher-container .scores-teacher-board .scores-teacher-head h1 {
    font-size: 1.5rem;
    color: #555555;
    margin: 0;
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body {
    width: 100%;
    padding:1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-head {
    width: 100%;
    height: 65px;
    background-color: #00AEEF;
    display: flex;
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-head .table-teacher-head-left {
    width: 50%;
    height: 100%;
    padding: 0 0.5rem;
    display: flex;
    align-items: center;
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-head .table-teacher-head-left p {
    color: #fff;
    font-weight: 500;
    font-size: 13px; 
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-head .table-teacher-head-right {
    width: 50%;
    height: 100%;
    padding: 0 0.5rem;
    display: flex;
    align-items: center;
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-head .table-teacher-head-right .teacher-score{
    width: 25%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-right: 1px solid #d5d5d5;
    border-left: 1px solid #d5d5d5;
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-head .table-teacher-head-right .teacher-score p {
    color: #fff;
    font-weight: 500;
    font-size: 13px;   
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-head .table-teacher-head-right .teacher-questions{
    width: 25%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-head .table-teacher-head-right .teacher-questions p {
    color: #fff;
    font-weight: 500;  
    font-size: 13px;  
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-head .table-teacher-head-right .teacher-time-taken{
    width: 25%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-head .table-teacher-head-right .teacher-time-taken p {
    color: #fff;
    font-weight: 500;  
    font-size: 13px; 
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-head .table-teacher-head-right .teacher-last-practiced{
    width: 25%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-head .table-teacher-head-right .teacher-last-practiced p {
    color: #fff;
    font-weight: 500;  
    font-size: 13px; 
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-body {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-body .scores-teacher-accordion {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 1px;
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-body .scores-teacher-accordion .teacher-accordion {
    background-color: #eee;
    color: #444;
    cursor: pointer;
    padding: 1rem 0.5rem;
    width: 100%;
    border: none;
    text-align: left;
    outline: none;
    font-size: 15px;
    transition: all 0.4s ease-in-out;
    display: flex;
    align-items: center;
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-body .scores-teacher-accordion .teacher-accordion h1{
    font-size: 1rem;
    color: #5b8a9b;
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-body .scores-teacher-accordion .teacher-accordion p{
    font-size: 13px;
    color: #5b8a9b;
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-body .scores-teacher-accordion .teacher-panel {
    display: none;
    background-color: white;
    overflow: hidden;
    flex-direction: column;
}

.scores-teacher-container .scores-teacher-board .scores-teacher-body .scores-teacher-table-body .scores-teacher-accordion .teacher-panel .stat-teacher-panel {
