.layout-container {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    overflow: hidden;
    flex-direction: column;
    background: linear-gradient(0deg, #1e1e24, #1e1e2f);
    z-index: 999;
  }
  
  /* dashboard-header */
  .layout-container .header {
    width: 95%;
    height: 7vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 999;
  }
  .layout-container .header .header-menu {
    display: flex;
    align-items: center;
  }
  .layout-container .header .header-menu .header-menu-icon {
    width: 20px;
    height: 20px;
    cursor: pointer;
  }
  
  .layout-container .header .header-menu h1 {
    margin: 0 1rem;
    font-size: 1.25rem;
    font-weight: normal;
    color: #fff;
  }
  
  .layout-container .header .header-buttons {
    display: flex;
    align-items: center;
    position: relative;
  }
  
  .layout-container .header .header-buttons i {
    color: #fff;
    font-size: 1.2rem;
    position: relative;
    cursor: pointer;
  }
  .layout-container .header .header-buttons i .noti {
    width: 10px;
    height: 10px;
    border-radius: 5px;
    background-color: #1d8cf8;
    position: absolute;
    right: -2px;
    top: -2px;
  }
  
  .layout-container .header .header-buttons .profile {
    display: flex;
    align-items: center;
    position: relative;
  }
  
  .layout-container .header .header-buttons .profile .profile-logo {
    width: 35px;
    height: 35px;
    object-fit: cover;
    border-radius: 25px;
    background-color: #fff;
    margin-right: 0.5rem;
    cursor: pointer;
  }
  
  .layout-container .header .header-buttons .profile .profile-name {
    color: #fff;
    margin-right: 0.5rem;
    font-size: 1rem;
    font-weight: 400;
    cursor: pointer;
  }
  
  .layout-container .header .header-buttons .profile .profile-menu {
    position: absolute;
    right: 0;
    width: 150px;
    background: linear-gradient(0deg, #1e1e24, #1e1e2f);
    top: 6vh;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
  }
  
  .layout-container .header .header-buttons .profile .profile-menu p {
    width: 100%;
    margin: 0.25rem 0;
    padding: 0.5rem 0;
    color: #fff;
    font-size: 1rem;
    font-weight: 400;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .layout-container .header .header-buttons .profile .profile-menu a {
    width: 100%;
    margin: 0.25rem 0;
    padding: 0.5rem 0;
    color: #1d8cf8;
    font-size: 1rem;
    font-weight: 400;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
  }
  
  .layout-container .header .header-buttons .profile .profile-menu p:hover {
    background-color: #1d8cf8;
    color: #fff;
  }
  
  .layout-container .header .header-buttons .profile .profile-menu a:hover {
    background-color: #1d8cf8;
    color: #fff;
  }
  
  /* dashboard-body */
  .layout-container .body {
    width: 100%;
    height: 93vh;
    display: flex;
    overflow: hidden;
    background-color: white;
  }
  
  /* dashboard-sidebar */
  .layout-container .body .sidebar-full {
    width: 16%;
    height: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    margin-top: 1rem;
  }
  
  .layout-container .body .sidebar-mini {
    width: 10%;
    height: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    margin-top: 1rem;
  }
  .layout-container .body .sidebar-full .sidebar-content::before {
    content: "";
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-bottom: 6px solid #1d8cf8;
    position: absolute;
    top: -6px;
    left: 11%;
    transition: top 0.3s ease-in-out;
  }
  .layout-container .body .sidebar-mini .sidebar-content::before {
    content: "";
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-bottom: 6px solid #1d8cf8;
    position: absolute;
    top: -6px;
    left: 20%;
    transition: top 0.3s ease-in-out;
  }
  .layout-container .body .sidebar-full .sidebar-content {
    width: 85%;
    background: linear-gradient(0deg, #3358f4, #1d8cf8);
    border-radius: 5px;
    position: absolute;
    top: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .layout-container .body .sidebar-mini .sidebar-content {
    width: 75%;
    background: linear-gradient(0deg, #3358f4, #1d8cf8);
    border-radius: 5px;
    position: absolute;
    top: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .layout-container .body .sidebar-content {
    padding: 1rem 0;
  }
  .layout-container .body .sidebar-content h1 {
    font-size: 1.25rem;
    color: #fff;
    font-weight: 100;
    margin: 0 0 1rem 0;
  }
  
  .layout-container .body .sidebar-content .menu {
    display: flex;
    width: 100%;
    padding: 0.8rem 0;
    cursor: pointer;
    flex-direction: column;
  }
  
  .layout-container .body .sidebar-content .menu .menu-item {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
  }
  
  .layout-container .body .sidebar-content .menu .menu-item .active {
    width: 6px;
    height: 6px;
    border-radius: 5px;
    background-color: #fff;
    position: absolute;
    left: 20px;
  }
  .layout-container .body .sidebar-content .menu .menu-item i {
    font-size: 1rem;
    color: #fff;
    margin: 0 0.5rem 0 2.5rem;
    width: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .layout-container .body .sidebar-content .menu .menu-item i:nth-child(3) {
    margin: 0 0 0 0.5rem;
  }
  .layout-container .body .sidebar-mini .sidebar-content .menu .menu-item {
    justify-content: center;
  }
  
  .layout-container .body .sidebar-mini .sidebar-content .menu .menu-item i {
    margin: 0;
  }
  
  .layout-container .body .sidebar-content .menu .menu-item p {
    font-size: 12px;
    color: #fff;
    font-weight: 400;
    margin: 0;
    text-transform: uppercase;
  }
  
  .layout-container .body .sidebar-content .menu .sub-menu {
    display: flex;
    flex-direction: column;
    margin-top: 0.5rem;
    margin: 1rem 1.5rem 0 1.5rem;
    background-color: rgba(30, 140, 255);
    border-radius: 5px;
  }
  
  .layout-container .body .sidebar-content .menu .sub-menu .menu-item {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0.8rem 0;
    position: relative;
    cursor: pointer;
  }
  
  .layout-container .body .sidebar-mini .sidebar-content .menu .sub-menu {
    /* justify-content: center; */
    margin-left: 0.5rem;
    margin-right: 0.5rem;
  }
  
  .layout-container .body .sidebar-content .menu .sub-menu .menu-item .active {
    width: 6px;
    height: 6px;
    border-radius: 5px;
    background-color: #fff;
    position: absolute;
    left: 20px;
  }
  
  .layout-container .body .sidebar-content .menu .sub-menu .menu-item i {
    font-size: 0.9rem;
  }
  
  .layout-container .body .sidebar-content .menu .sub-menu p {
    font-size: 12px;
    color: #fff;
    display: flex;
    align-items: center;
    text-transform: uppercase;
  }
  
  .layout-container .body .sidebar-content .menu .sub-menu p .active {
    width: 6px;
    height: 6px;
    border-radius: 5px;
    background-color: #fff;
    margin-right: 0.5rem;
  }
  
  /* dashboard-content */
  .layout-container .body .content-full {
    width: 89%;
    height: 95%;
    transition: width 0.3s ease-in-out;
    /* overflow-y: scroll; */
    margin-top: 1rem;
    border-radius: 5px;
    box-shadow: 0 1px 6px 0px #DEE0E2;
  }
  
  .layout-container .body .content-mini {
    width: 83%;
    height: 95%;
    background-color: #fff;
    transition: width 0.3s ease-in-out;
    margin-top: 1rem;
    border-radius: 5px;
    box-shadow: 0 1px 6px 0px #DEE0E2;
  }
  