.quesSession-board{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.quesSession-board .quesSession-head{
    width: 90%;
    padding: 1rem 0;
    display: flex;
    align-items: center;
}

.quesSession-board .quesSession-head h1 {
    font-size: 1rem;
    color: #555555;
    margin: 0;
}

.quesSession-board .quesSession-head #back-arrow {
    cursor: pointer;
    font-size: 1.2rem;
    margin-right: 10px;
    border-radius: 50%;
    transition: all 0.2s ease-in-out;
}

.quesSession-board .quesSession-head #back-arrow:hover {
    background-color: 	rgba(255,255,102,0.35);
}

.quesSession-board .quesSession-stats-summary{
    width: 90%;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.quesSession-board .quesSession-stats-summary .summary-head {
    width: 100%;
    padding: 1rem;
    background-color: #00AEEF;
    color: #fff;
    display: flex;
    align-items: center;
}

.quesSession-board .quesSession-stats-summary .summary-head h1 {
    margin: 0;
    font-size: 1.5rem;
}

.quesSession-board .quesSession-stats-summary .summary-body {
    width: 100%;
    padding: 1rem;
    color: #fff;
    display: flex;
    flex-direction: column;
    border-right: 1px solid #c5c5c5;
    border-left: 1px solid #c5c5c5;
    border-bottom: 1px solid #c5c5c5;
}

.quesSession-board .quesSession-stats-summary .summary-body .summary-body-top {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 2rem;
}

.quesSession-board .quesSession-stats-summary .summary-body .summary-body-top .stats-box {
    width: 33%;
    height: 100%;
    display: flex;
    border-right: 1px solid #c5c5c5;
}

.quesSession-board .quesSession-stats-summary .summary-body .summary-body-top .stats-box:last-child {
    border: none;
}

.quesSession-board .quesSession-stats-summary .summary-body .summary-body-top .stats-box .left {
    width: 45%;
    display: flex;
    justify-content: flex-end;
    padding: 0.5rem 1rem;
}

.quesSession-board .quesSession-stats-summary .summary-body .summary-body-top .stats-box .left #star{
    color: #52B700;
}

.quesSession-board .quesSession-stats-summary .summary-body .summary-body-top .stats-box .left #pencil {
    color: #26A0FC;
}

.quesSession-board .quesSession-stats-summary .summary-body .summary-body-top .stats-box .left #clock {
    color: #E77339;
}

.quesSession-board .quesSession-stats-summary .summary-body .summary-body-top .stats-box .left p {
    font-size: 2.5rem;
}

.quesSession-board .quesSession-stats-summary .summary-body .summary-body-top .stats-box .right {
    width: 55%;
    display: flex;
    flex-direction: column;
    padding:1rem;
}

.quesSession-board .quesSession-stats-summary .summary-body .summary-body-top .stats-box .right p {
    font-size: 14px;
    color: #666666;
    font-weight: 500;
}

.quesSession-board .quesSession-stats-summary .summary-body .summary-body-top .stats-box .right h1 {
    color: #666666;
}


.quesSession-board .quesSession-questions{
    width: 90%;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.quesSession-board .quesSession-questions .questions-head {
    width: 100%;
    padding: 1rem;
    background-color: #00AEEF;
    color: #fff;
    display: flex;
    align-items: center;
}

.quesSession-board .quesSession-questions .questions-head h1 {
    margin: 0;
    font-size: 1.5rem;
}

.quesSession-board .quesSession-questions .questions-body {
    width: 100%;
    padding: 1rem 1.5rem;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
}

