.teacher-layout-container {
	width: 100%;
	height: 100vh;
	display: flex;
	align-items: center;
	overflow: hidden;
	flex-direction: column;
	background: linear-gradient(0deg, #1e1e24, #1e1e2f);
	z-index: 999;
}

/* dashboard-header */
.teacher-layout-container .header {
	width: 95%;
	height: 7vh;
	display: flex;
	align-items: center;
	justify-content: space-between;
	z-index: 999;
}

.teacher-layout-container .header .header-menu {
	display: flex;
	align-items: center;
}

.teacher-layout-container .header .header-menu .header-menu-icon {
	width: 20px;
	height: 20px;
	cursor: pointer;
}

.teacher-layout-container .header .header-menu h1 {
	margin: 0 1rem;
	font-size: 1.25rem;
	font-weight: normal;
	color: #fff;
}

.teacher-layout-container .header .header-buttons {
	display: flex;
	align-items: center;
	position: relative;
}

.teacher-layout-container .header .header-buttons i {
	color: #fff;
	font-size: 1.2rem;
	position: relative;
	cursor: pointer;
}
.teacher-layout-container .header .header-buttons i .noti {
	width: 10px;
	height: 10px;
	border-radius: 5px;
	background-color: #1d8cf8;
	position: absolute;
	right: -2px;
	top: -2px;
}

.teacher-layout-container .header .header-buttons .profile {
	display: flex;
	align-items: center;
	position: relative;
}

.teacher-layout-container .header .header-buttons .profile .profile-logo {
	width: 35px;
	height: 35px;
	object-fit: cover;
	border-radius: 25px;
	background-color: #fff;
	margin-right: 0.5rem;
	cursor: pointer;
}

.teacher-layout-container .header .header-buttons .profile .profile-name {
	color: #fff;
	margin-right: 0.5rem;
	font-size: 1rem;
	font-weight: 400;
	cursor: pointer;
}

.teacher-layout-container .header .header-buttons .profile .profile-menu {
	position: absolute;
	right: 0;
	width: 150px;
	background: linear-gradient(0deg, #1e1e24, #1e1e2f);
	top: 6vh;
	border-radius: 5px;
	display: flex;
	flex-direction: column;
	align-items: center;
	box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
}

.teacher-layout-container .header .header-buttons .profile .profile-menu p {
	width: 100%;
	margin: 0.25rem 0;
	padding: 0.5rem 0;
	color: #fff;
	font-size: 1rem;
	font-weight: 400;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
}

.teacher-layout-container .header .header-buttons .profile .profile-menu a {
	width: 100%;
	margin: 0.25rem 0;
	padding: 0.5rem 0;
	color: #1d8cf8;
	font-size: 1rem;
	font-weight: 400;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	text-decoration: none;
}

.teacher-layout-container .header .header-buttons .profile .profile-menu p:hover {
	background-color: #1d8cf8;
	color: #fff;
}

.teacher-layout-container .header .header-buttons .profile .profile-menu a:hover {
	background-color: #1d8cf8;
	color: #fff;
}

/* dashboard-body */
.teacher-layout-container .body {
	width: 100%;
	height: 93vh;
	display: flex;
	overflow: hidden;
	background-color: white;
}

/* dashboard-sidebar */
.teacher-layout-container .body .sidebar-full {
	width: 16%;
	height: 100%;
	position: relative;
	display: flex;
	justify-content: center;
	margin-top: 1rem;
	overflow-y: scroll;
}

.teacher-layout-container .body .sidebar-mini {
	width: 10%;
	height: 100%;
	position: relative;
	display: flex;
	justify-content: center;
	margin-top: 1rem;
}

.teacher-layout-container .body .sidebar-full .sidebar-content::before {
	content: "";
	border-left: 5px solid transparent;
	border-right: 5px solid transparent;
	border-bottom: 6px solid #1d8cf8;
	position: absolute;
	top: -6px;
	left: 15%;
	transition: top 0.3s ease-in-out;
}

.teacher-layout-container .body .sidebar-mini .sidebar-content::before {
	content: "";
	border-left: 5px solid transparent;
	border-right: 5px solid transparent;
	border-bottom: 6px solid #1d8cf8;
	position: absolute;
	top: -6px;
	left: 20%;
	transition: top 0.3s ease-in-out;
}

.teacher-layout-container .body .sidebar-full .sidebar-content {
	width: 90%;
	background: linear-gradient(0deg, #3358f4, #1d8cf8);
	border-radius: 5px;
	position: absolute;
	top: 0;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.teacher-layout-container .body .sidebar-mini .sidebar-content {
	width: 75%;
	background: linear-gradient(0deg, #3358f4, #1d8cf8);
	border-radius: 5px;
	position: absolute;
	top: 0;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.teacher-layout-container .body .sidebar-content {
	padding: 1rem 0;
}
.teacher-layout-container .body .sidebar-content h1 {
	font-size: 1.25rem;
	color: #fff;
	font-weight: 100;
}

.teacher-layout-container .body .sidebar-content .menu {
	display: flex;
	width: 100%;
	padding: 0.5rem 0;
	cursor: pointer;
	flex-direction: column;
}

.teacher-layout-container .body .sidebar-content .menu .menu-item {
	width: 100%;
	display: flex;
	flex-direction: row;
	align-items: center;
	position: relative;
	padding: 0.25rem 0;
	padding-left: 25px;
	user-select: none;
}

.teacher-layout-container .body .sidebar-content .menu .menu-item .active {
	width: 6px;
	height: 6px;
	border-radius: 5px;
	background-color: #fff;
	position: absolute;
	left: 15px;
}

.teacher-layout-container .body .sidebar-content .menu .menu-item i {
	font-size: 1rem;
	color: #fff;
	width: 25px;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-right: 5px;
}

.teacher-layout-container .body .sidebar-content .menu .menu-item p {
	font-size: 12px;
	color: #fff;
	font-weight: 400;
	margin: 0;
	text-transform: uppercase;
}

.teacher-layout-container .body .sidebar-content .sub-menu {
	display: flex;
	width: 100%;
	padding: 0 0 0 0;
	padding-left: 20px;
	cursor: pointer;
	flex-direction: column;
}

.teacher-layout-container .body .sidebar-content .sub-menu .sub-menu-item {
	width: 100%;
	display: flex;
	flex-direction: row;
	align-items: center;
	position: relative;
	padding: 1rem 0;
	padding-left: 35px;
	user-select: none;
}

.teacher-layout-container .body .sidebar-content .sub-menu .sub-menu-item:hover {
	background-color: rgba(0, 0, 0, 0.15);
}

.teacher-layout-container .body .sidebar-content .sub-menu .sub-menu-item .active {
	width: 6px;
	height: 6px;
	border-radius: 5px;
	background-color: #fff;
	position: absolute;
	left: 20px;
}
.teacher-layout-container .body .sidebar-content .sub-menu .sub-menu-item p {
	font-size: 14px;
	color: #fff;
	font-weight: 400;
}

.teacher-layout-container .body .sidebar-content .sub-menu .sub-menu-item i {
	font-size: 1rem;
	color: #fff;
	padding: 0;
	margin: 0;
	margin-left: 5px;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-right: 5px;
	transform: translateY(1px);
}

/* dashboard-content */
.teacher-layout-container .body .content-full {
	width: 89%;
	height: 95%;
	transition: width 0.3s ease-in-out;
	/* overflow-y: scroll; */
	margin-top: 1rem;
	border-radius: 5px;
	box-shadow: 0 1px 6px 0px #dee0e2;
}

.teacher-layout-container .body .content-mini {
	width: 83%;
	height: 95%;
	background-color: #fff;
	transition: width 0.3s ease-in-out;
	margin-top: 1rem;
	border-radius: 5px;
	box-shadow: 0 1px 6px 0px #dee0e2;
}
