.progress-container {
    width: 80%;
    padding: 2rem 0;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.15);
    display: flex;
    justify-content: center;
}

.progress-container .progress-board{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.progress-container .progress-board .progress-head {
    width: 100%;
    padding: 0 1rem;
    display: flex;
    align-items: center;
}

.progress-container .progress-board .progress-head h1 {
    font-size: 1.5rem;
    color: #555555;
    margin: 0;
}

.progress-container .progress-board .progress-body {
    width: 100%;
    padding:1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.progress-container .progress-board .progress-body .progress-table-head {
    width: 100%;
    height: 65px;
    background-color: #00AEEF;
    display: flex;
}

.progress-container .progress-board .progress-body .progress-table-head .table-head-left {
    width: 50%;
    height: 100%;
    padding: 0 0.5rem;
    display: flex;
    align-items: center;
}

.progress-container .progress-board .progress-body .progress-table-head .table-head-left p {
    color: #fff;
    font-weight: 500;
    font-size: 13px; 
}

.progress-container .progress-board .progress-body .progress-table-head .table-head-right {
    width: 50%;
    height: 100%;
    padding: 0 0.5rem;
    display: flex;
    align-items: center;
}

.progress-container .progress-board .progress-body .progress-table-head .table-head-right .time-spent{
    width: 20%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.progress-container .progress-board .progress-body .progress-table-head .table-head-right .time-spent p {
    color: #fff;
    font-weight: 500;
    font-size: 13px;   
}

.progress-container .progress-board .progress-body .progress-table-head .table-head-right .questions{
    width: 20%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.progress-container .progress-board .progress-body .progress-table-head .table-head-right .questions p {
    color: #fff;
    font-weight: 500;  
    font-size: 13px;  
}

.progress-container .progress-board .progress-body .progress-table-head .table-head-right .score-improvement{
    width: 60%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.progress-container .progress-board .progress-body .progress-table-head .table-head-right .score-improvement p {
    color: #fff;
    font-weight: 500;  
    font-size: 13px; 
}

.progress-container .progress-board .progress-body .progress-table-body {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.progress-container .progress-board .progress-body .progress-table-body .progress-accordion {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 1px;
}

.progress-container .progress-board .progress-body .progress-table-body .progress-accordion .accordion {
    background-color: #eee;
    color: #444;
    cursor: pointer;
    padding: 1rem 0.5rem;
    width: 100%;
    border: none;
    text-align: left;
    outline: none;
    font-size: 15px;
    transition: all 0.4s ease-in-out;
    display: flex;
    align-items: center;
}

.progress-container .progress-board .progress-body .progress-table-body .progress-accordion .accordion h1{
    font-size: 1rem;
    color: #5b8a9b;
}

.progress-container .progress-board .progress-body .progress-table-body .progress-accordion .accordion p{
    font-size: 13px;
    color: #5b8a9b;
}

.progress-container .progress-board .progress-body .progress-table-body .progress-accordion .panel {
    display: none;
    background-color: white;
    overflow: hidden;
    flex-direction: column;
}

.progress-container .progress-board .progress-body .progress-table-body .progress-accordion .panel .stat-panel {
    width: 100%;
    display: flex;
    align-items: center;
}

.progress-container .progress-board .progress-body .progress-table-body .progress-accordion .panel .stat-panel:hover {
    background-color: #f5f7fa;
}

.progress-container .progress-board .progress-body .progress-table-body .progress-accordion .panel .stat-panel .panel-left {
    width: 50%;
    padding: 0.5rem 1.5rem;
    display: flex;
    align-items: center;
}

.progress-container .progress-board .progress-body .progress-table-body .progress-accordion .panel .stat-panel .panel-left p {
    font-size: 13px;
    color: #616161;
}

.progress-container .progress-board .progress-body .progress-table-body .progress-accordion .panel .stat-panel .panel-left p .upwards {
    color: green;
}

.progress-container .progress-board .progress-body .progress-table-body .progress-accordion .panel .stat-panel .panel-left p .downwards {
    color: red;
}

.progress-container .progress-board .progress-body .progress-table-body .progress-accordion .panel .stat-panel .panel-right {
    width: 50%;
    height: 100%;
    padding: 0 0 0 0.5rem;
    display: flex;
    align-items: center;
}

.progress-container .progress-board .progress-body .progress-table-body .progress-accordion .panel .stat-panel .panel-right .time-spent{
    width: 20%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.progress-container .progress-board .progress-body .progress-table-body .progress-accordion .panel .stat-panel .panel-right .time-spent p {
    font-size: 13px; 
    color: #616161;  
}

.progress-container .progress-board .progress-body .progress-table-body .progress-accordion .panel .stat-panel .panel-right .questions{
    width: 20%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.progress-container .progress-board .progress-body .progress-table-body .progress-accordion .panel .stat-panel .panel-right .questions p {

    font-size: 13px; 
    color: #616161;  
}

.progress-container .progress-board .progress-body .progress-table-body .progress-accordion .panel .stat-panel .panel-right .score-improvement{
    width: 60%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.progress-container .progress-board .progress-body .progress-table-body .progress-accordion .panel .stat-panel .panel-right .score-improvement p {
    font-size: 13px; 
    margin: 0 !important;
    color: #616161;
}

  