@font-face {
  font-family: "mulish";
  src : url('./layout/assets/static/Mulish-Regular.ttf');
}
* {
    box-sizing: border-box;
    font-family: "mulish";
    margin: 0;
    padding: 0;
  }
  body {
    background-color: #fff;
  }
  a {
    text-decoration: none;
  }
  p{
    margin: 0;
  }
  li {
    list-style: none;
  }
  
  ::-webkit-scrollbar {
    width: 4px;
  }
  
  ::-webkit-scrollbar-thumb {
    background-color: #f0f0f0;
    border-radius: 4px;
  }
  
