.teacher-analytics {
    width: 100%;
    height: 72vh;
    overflow-y: scroll;
}
.select-analytics-type {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    /* padding: 1rem 0; */
    background-color: #f5f7fa;
    border: 1px solid #c5c5c5;
}

.select-analytics-type .tab-type {
    width: 15%;
    border-right: 1px solid #c5c5c5;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}

.select-analytics-type .tab-type:last-child {
    border: none;
}

.select-analytics-type .tab-type h1 {
    font-size: 1.2rem;
    color: #717171;
}

.select-analytics-type .tab-type p {
    font-size: 12px;
    color: #717171;
}

.select-analytics-type .tab-type:hover h1,
.select-analytics-type .tab-type:hover p {
    color: #26A0FC;
}

.select-analytics-type .tab-type.active {
    border-bottom: 5px solid #26A0FC;
}

.select-analytics-type .tab-type.active h1,
.select-analytics-type .tab-type.active p {
    color: #26A0FC;
}

.report-whiteboard {
    width: 100%;
    padding: 2rem 0;
    display: flex;
    align-items: center;
    align-items: center;
    justify-content: center;
}