.my-library-container{
    width: 100%;
    min-height: 80vh;
    padding: 3.5rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.my-library-container .my-library-wrapper {
    width: 50%;
    padding: 1rem;
    background-color: #f5f7fa;
    border-radius: 6px;
    box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.my-library-container .my-library-wrapper .library-head {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 2rem;
}

.my-library-container .my-library-wrapper .library-head input {
    width: 45%;
    padding: 0 0.5rem;
    outline: none;
}

.my-library-container .my-library-wrapper .library-head .filter-box {
    border: 1.5px solid #3FD3C9;
    padding: 0 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    cursor: pointer;
}

.my-library-container .my-library-wrapper .library-head .filter-box p {
    color: #3FD3C9;
}

.my-library-container .my-library-wrapper .library-head .filter-box:hover {
    background-color: #3FD3C9;
    color: #fff;
}

.my-library-container .my-library-wrapper .library-head .filter-box:hover p {
    color: #fff;
}

.my-library-container .my-library-wrapper .library-head .filter-box.active {
    background-color: #3FD3C9;
    color: #fff;
}

.my-library-container .my-library-wrapper .library-head .filter-box.active p {
    color: #fff;
}

.my-library-container .my-library-wrapper .library-body {
    width: 100%;
    height: 50vh;
    display: flex;
	flex-direction: column;
	align-items: center;
}


.my-library-container .my-library-wrapper .library-body .library-box {
	height: 100px;
	width: 100%;
	margin-bottom: 1rem;
	cursor: pointer;
	padding: 1rem 1rem;
	background-color: #fff;
	border-radius: 5px;
	border: 1px solid #ddd;
	display: flex;
	justify-content: space-between;
	position: relative;
	margin: 1rem 0;
	text-decoration: none;
}

.my-library-container .my-library-wrapper .library-body .library-box img {
	height: 70px;
}

.my-library-container .my-library-wrapper .library-body .library-box .topic-name {
	width: 70%;
	height: 100%;
	padding: 0 1.5rem;
	display: flex;
	flex-direction: column;
}

.my-library-container .my-library-wrapper .library-body .library-box .topic-name h1 {
	margin: 0;
	font-size: 18px;
	color: #333333;
}

.my-library-container .my-library-wrapper .library-body .library-box .topic-name p {
	margin: 5px 0;
	font-size: 14px;
	color: #333333;
}

.my-library-container .my-library-wrapper .library-body .library-box .topic-name .keywords {
	display: flex;
	align-items: center;
}

.my-library-container .my-library-wrapper .library-body .library-box .topic-name .keywords p {
	margin: 0 0.25rem 0 0;
	background-color: darkgray;
	padding: 0.1rem 0.5rem;
	font-size: 10px;
	border-radius: 5px;
	color: #fff;
}

.my-library-container .my-library-wrapper .library-body .library-box .topic-date {
	width: 30%;
	height: 100%;
	display: flex;
	align-items: flex-end;
	flex-direction: column;
}

.my-library-container .my-library-wrapper .library-body .library-box .topic-date button {
	width: 120px;
	height: 30px;
	border: none;
	background-color: lightgrey;
	border-radius: 5px;
	color: rgb(44, 44, 44);
	margin-top: 10px;
}
