 .iigp-navbar-container {
  width: 100%;
  height: 10vh;
  padding: 0 3.5rem;
  position: fixed;
  top: 6vh;
  z-index: 99;
  background-color: #205C6D;
  /* background-color: #274067; */
  display: flex;
  align-items: center;
  justify-content: center;
}

 .iigp-navbar-container .iigp-logo {
  width: 50%;
  height: 100%;
  display: flex;
  align-items: center;
}

 .iigp-navbar-container .iigp-logo img {
  height: 30px;
}

 .iigp-navbar-container .menu-links {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
}

 .iigp-navbar-container .menu-links .link-box {
  width: 20%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}


  .iigp-navbar-container
  .menu-links
  .link-box::before {
  content: "";
  position: absolute;
  width: 0;
  bottom: -5px;
  height: 4px;
  background-color: green;
}


  .iigp-navbar-container
  .menu-links
  .link-box:hover::before {
  content: "";
  position: absolute;
  width: 70px;
  bottom: 0px;
  height: 4px;
  background-color: #0eb582;
  border-radius: 3px;
  transition: width 0.5s ease-in-out;
  transform-origin: left;
}

.iigp-navbar-container
.menu-links
.link-box:hover p {
  color: #0eb582;
}

 .iigp-navbar-container .menu-links .link-box p {
   font-size: 14px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-family: Roboto, sans-serif;
}

 .iigp-navbar-container .menu-links .link-box span {
  color: #fff;
  font-weight: 500;
  cursor: pointer;
  line-height: normal;
 }

 .iigp-navbar-container .menu-links .link-user {
  width: 40%;
  padding: 0 1rem;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  cursor: pointer;
}

.iigp-navbar-container .menu-links .link-user p {
  font-size: 14px;
 color: #fff;
 /* font-weight: bold; */
 cursor: pointer;
 /* font-family: Roboto, sans-serif; */
}

.iigp-navbar-container .menu-links .link-user span {
  color: #fff;
  font-weight: 500;
  cursor: pointer;
  line-height: normal;
 }

  .iigp-navbar-container
  .menu-links
  .link-user
  #user-icon {
  height: 30px;
  width: 30px;
  object-fit: cover;
  border-radius: 50%;
}


  .iigp-navbar-container
  .menu-links
  .link-user
  #arrow-icon {
  height: 12px;
}

/* Analytics sub menu */

.iigp-navbar-container .menu-links .nav-analytics-menu {
  background-color: #fff;
  border-radius: 5px;
  width: 180px;
  height: 0;
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.25);
  position: absolute;
  top: 8vh;
  right: 50%;
  overflow-y: hidden;
  transition-property: all;
  transition-duration: .5s;
  transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
}

.iigp-navbar-container .menu-links .nav-analytics-menu.active {
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.25);
  border: #ccc 1px solid;
}

.iigp-navbar-container .menu-links .nav-analytics-menu .menu-list{
  display: none;
}

.iigp-navbar-container .menu-links .nav-analytics-menu.active .menu-list {
  width: 100%;
  height: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem 0;
  cursor: pointer;
}

.iigp-navbar-container .menu-links .nav-analytics-menu.active .menu-list a {
  text-decoration: none;
  color: #000;
}

.iigp-navbar-container .menu-links .nav-analytics-menu.active .menu-list:hover {
  background-color: #EBF3FA;
}


