.modal-content .my-profile-container {
  width: 100%;
  height: 80vh;
  background-color: #fff;
  border-radius: 5px;
  position: relative;
  /* display: flex; */
}

.modal-content .my-profile-container .trophy-container {
  width: 100%;
  height: 100%;
  clip-path: circle(10% at 0 0);
  background-color: blue;
  cursor: pointer;
  transition: clip-path 0.5s ease-in-out;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 999;
}

.modal-content .my-profile-container .trophy-container .trophy-logo {
  width: 50px;
  height: 50px;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content .my-profile-container .trophy-container .trophy-logo img {
  height: 30px;
}

.modal-content .my-profile-container .trophy-container p{
  color: #fff;
  font-size: 1.2rem;
}

.modal-content .my-profile-container .trophy-container:hover {
  clip-path: circle(142.2% at 0 0);
}

.modal-content .my-profile-container .trophy-container:hover img{
  display: none;
}

.modal-content .my-profile-container .trophy-container.active {
  clip-path: circle(142.2% at 0 0);
}

.modal-content .my-profile-container .trophy-container.active img {
  display: none;
}

.modal-content .my-profile-container .my-profile-head{
  width: 100%;
  height: 100%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
}

.modal-content .my-profile-container .my-profile-head .my-profile-left {
  width: 25%;
  height: 100%;
  background-color: #4cd6c6;
  background: linear-gradient( 180deg, rgba(2, 196, 215, 1) 0%, rgba(126, 226, 186, 1) 100% );
  /* background-color: #FEC341; */
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.modal-content .my-profile-container .my-profile-head .my-profile-left img {
  width: 100%;
  height: 270px;
  position: absolute;
  top: 10%;
  left: 50%;
  object-fit: cover;
  border-radius: 5px 5px 50px 5px;
}

.modal-content .my-profile-container .my-profile-head .my-profile-right {
  width: 75%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #F5F5F5;
}


.modal-content .my-profile-container .my-profile-head .my-profile-right .my-profile-info {
  width: 60%;
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* background-color: red; */
}

.modal-content .my-profile-container .my-profile-head .my-profile-right .my-profile-info .my-profile-name {
  background-color: #4cd6c6;
  background: linear-gradient( 180deg, rgba(2, 196, 215, 1) 0%, rgba(126, 226, 186, 1) 100% );
  padding: 0.6rem;
  border-radius: 3px;
  margin-bottom: 10px;
}

.modal-content .my-profile-container .my-profile-head .my-profile-right .my-profile-info .my-profile-name p {
  text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  color: #fff;
  font-size: 1.2  rem;
}

    