.superadmin-home-container {
    width: 100%;
    height: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    background-color: #f9f9f9;
}

.superadmin-home-container .stats-header {
    width: 100%;
    padding: 4rem 0;
    /* background-color: yellow; */
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.superadmin-home-container .stats-header .stats-card-container {
    width: 23%;
    height: 150px;
    padding: 0 1rem;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.15);
    display: flex;
    flex-direction: column;
}

.superadmin-home-container .stats-header .stats-card-container .stats-card-body {
    width: 100%;
    height: 70%;
    border-radius: 10px 10px 0 0;
    border-bottom: 1.5px solid #eeeeee;
    display: flex;
    align-items: center;
    justify-content: center;
}

.superadmin-home-container .stats-header .stats-card-container .stats-card-body .img-part {
    width: 40%;
    height: 100%;
    position: relative;
}

.superadmin-home-container .stats-header .stats-card-container .stats-card-body .img-part .img-box {
    position: absolute;
    top: -30px;
    right: 0;
    height: 100%;
    width: 100%;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.y-box {
    background: linear-gradient(60deg, #ffa726, #fb8c00);
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0,.14), 0 7px 10px -5px rgba(255, 152, 0,.4);
}

.b-box {
    background: linear-gradient(60deg, #26c6da, #00acc1);
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0,.14), 0 7px 10px -5px rgba(0, 172, 193,.4);
}

.g-box {
    background: linear-gradient(60deg, #66bb6a, #43a047);
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0,.14), 0 7px 10px -5px rgba(76, 175, 80,.4);
}

.r-box {
    background: linear-gradient(60deg, #ef5350, #e53935);
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0,.14), 0 7px 10px -5px rgba(244, 67, 54,.4);
}

.superadmin-home-container .stats-header .stats-card-container .stats-card-body .img-part .img-box img {
    height: 50%;
}

.superadmin-home-container .stats-header .stats-card-container .stats-card-body .stats-part {
    width: 60%;
    height: 100%;
    padding: 1rem 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-start;
}

.superadmin-home-container .stats-header .stats-card-container .stats-card-body .stats-part h1 {
    font-size: 14px;
    color: #999999;
    font-weight:400;
}

.superadmin-home-container .stats-header .stats-card-container .stats-card-body .stats-part p {
    font-size: 26px;
    color: #3c4858;
    font-weight: normal;
}

.superadmin-home-container .stats-header .stats-card-container .stats-card-foot {
    width: 100%;
    height: 30%;
    border-radius: 0 0 10px 10px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.superadmin-home-container .stats-header .stats-card-container .stats-card-foot img {
    height: 16px;
    margin-right: 5px;
}

.superadmin-home-container .stats-header .stats-card-container .stats-card-foot p {
    color: #999999;
    font-size: 14px;
}