.question-accordion {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 1px;
}

.question-accordion .accordion {
    background-color: #666666;
    color: #444;
    cursor: pointer;
    width: 100%;
    border: none;
    text-align: left;
    outline: none;
    font-size: 15px;
    transition: all 0.4s ease-in-out;
    display: flex;
    align-items: center;
}

.question-accordion .accordion .left-acc {
    width: 75%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 1rem 0.5rem;
}

.question-accordion .accordion .left-acc h1 {
    margin: 0;
    font-size: 14px;
    color: #fff;
}

.question-accordion .accordion .right-acc {
    width: 25%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.question-accordion .accordion .right-acc h1 {
    margin: 0;
    font-size: 14px;
    color: #fff;
}

.question-accordion .accordion .right-acc div {
    height: 100%;
    background-color: #464646;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
}

.question-accordion .accordion .right-acc div h1 {
    font-size: 18px;
}

.question-accordion .panel {
    display: none;
    background-color: white;
    overflow: hidden;
    flex-direction: column;
    border-right: 1px solid #c5c5c5;
    border-left: 1px solid #c5c5c5;
    border-bottom: 1px solid #c5c5c5;
}

.question-accordion .panel .panel-topic {
    width: 100%;
    padding: 1rem 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 5px;
    cursor: pointer;
    border-top: 10px solid #52B700;
}

.question-accordion .panel .panel-topic p {
    color: #000000;
}

.question-accordion .panel .panel-topic img {
    width: 60%;
    height: 60%;
    margin: 1rem 0;
}

.question-accordion .panel .panel-topic .correct {
    width: 100%;
    padding: 1rem 0;
    display: flex;
    align-items: center;
}

.question-accordion .panel .panel-topic .correct p {
    margin: 0;
}

.question-accordion .panel .panel-topic .correct span {
    margin: 0 1rem;
    width: 100px;
    padding: 0.5rem 0;
    color: #000000;
    background-color: #EAEDED;
    border: 1px solid #00AEEF;
    display: flex;
    align-items: center;
    justify-content: center;
}

.quesSession-board .quesSession-questions .questions-body .question-accordion .panel .panel-topic .actual {
    width: 100%;
    padding: 1rem 0;
    display: flex;
    align-items: center;
}

.quesSession-board .quesSession-questions .questions-body .question-accordion .panel .panel-topic .actual p {
    margin: 0;
}

.quesSession-board .quesSession-questions .questions-body .question-accordion .panel .panel-topic .actual #tick {
    color: #52B700;
}

.quesSession-board .quesSession-questions .questions-body .question-accordion .panel .panel-topic .actual #wrong {
    color: red;
}

.quesSession-board .quesSession-questions .questions-body .question-accordion .panel .panel-topic .actual span {
    margin: 0 1rem;
    width: 100px;
    padding: 0.5rem 0;
    color: #000000;
    background-color: #EAEDED;
    border: 1px solid #00AEEF;
    display: flex;
    align-items: center;
    justify-content: center;
}
