.students-container {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
}

.students-container .students-header {
	width: 100%;
	padding: 1rem;
	display: flex;
	background-color: #fff;
	border-bottom: 1px solid #c5c5c5;
	border-radius: 5px 5px 0 0;
}

.students-container .students-header .students-stats {
	width: 80%;
	display: flex;
	flex-direction: column;
}

.students-container .students-header .students-stats h1 {
	font-size: 22px;
}

.students-container .students-header .students-stats .officer-stats {
	width: 100%;
	padding: 0.5rem 0;
	display: flex;
	align-items: center;
}

.students-container .students-header .students-stats .officer-stats p {
	font-size: 14px;
	color: #999999;
}

.students-container .students-header .students-stats .officer-stats p span {
	color: #414141;
	font-weight: 500;
	font-size: 16px;
}

.students-container .students-header .students-add {
	width: 20%;
	display: flex;
	justify-content: center;
}

.students-container .students-header .students-add .add-officer-button {
	width: 80%;
	height: 50%;
	padding: 0.5rem 1rem;
	background: linear-gradient(90deg, #3358f4, #1d8cf8);
	border-radius: 6px;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.5);
	transition: all 0.1s;
}

.students-container .students-header .students-add .add-officer-button:hover {
	box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.75);
}

.students-container .students-header .students-add .add-officer-button img {
	height: 20px;
	margin-right: 20px;
}

.students-container .students-header .students-add .add-officer-button p {
	color: #fff;
}

.students-container .students-body {
	width: 100%;
	/* height: 100%; */
	padding: 0.5rem;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 0 0 5px 5px;
	overflow-y: scroll;
}

.container-group {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.image-container {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}
.image-container p {
	margin-right: 1rem;
}

.image-container .image-picker {
	width: 150px;
	height: 150px;
	overflow: hidden;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #e5e5e5;
	border-radius: 50%;
	box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.15);
}

.image-container .image-picker input {
	width: 100%;
	height: 100%;
	opacity: 0;
	position: absolute;
	top: 0;
	cursor: pointer;
}

.image-container img {
	width: 150px;
	height: 150px;
	border-radius: 50%;
	object-fit: cover;
	cursor: pointer;
}

.image-container .image-picker .image-icon {
	width: 50px;
	height: 50px;
	border-radius: 0;
}

.btn-primary {
	background: linear-gradient(90deg, #3358f4, #1d8cf8);
}

.success-text {
	font-size: 1rem;
	color: #218838;
}

.danger-text {
	font-size: 1rem;
	color: #de3700;
}

.action-buttons {
	width: 50px !important;
	height: 25px !important;
	font-size: 12px !important;
	margin: 0 0.25rem;
	display: flex;
	align-items: center;
	justify-content: center;
}

.tab-action-button {
	width: 100% !important;
	height: 35px !important;
	font-size: 14px !important;
}

.submit-action {
	height: 35px !important;
	font-size: 14px !important;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 0.25rem;
}

svg {
	margin: 0 0.25rem;
}

.modal-header .close {
	outline: none;
}

.school-info-list {
	width: 100%;
	display: flex;
	align-items: center;
	flex-wrap: wrap;
}
.student-photo {
	width: 30px;
	height: 30px;
	border-radius: 50%;
}
.students-container .students-body .student-behaviour-action {
	border-top: 2px solid #1e8af8;
	border-bottom: 2px solid #1e8af8;
	border-left: 2px solid #1e8af8;
	padding: 0 0.65rem;
	cursor: pointer;
	color: #1e8af8;
}

.students-container .students-body .student-behaviour-action.active {
	background-color: #1e8af8;
	color: #fff;
}

.students-container .students-body .student-behaviour-action:nth-child(1) {
	border-top-left-radius: 15px;
	border-bottom-left-radius: 15px;
	padding: 0 0.8rem;
}

.students-container .students-body .student-behaviour-action:nth-child(5) {
	border-right: 2px solid #1e8af8;
	border-top-right-radius: 15px;
	border-bottom-right-radius: 15px;
	padding: 0 0.8rem;
}

.students-container .students-body .student-behaviour-note {
	border: 2px solid #1e8af8;
	width: 30px;
	height: 30px;
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	color: #1e8af8;
	cursor: pointer;
}

.note-text-area {
	width: 100%;
	height: 130px;
	padding: 0.25rem;
	outline: none;
	border: 1px solid #acadaa;
	resize: none;
	border-radius: 5px;
}
