.student-homepage-container {
  width: 100%;
}

.student-homepage-container .student-home-banner {
  margin-top: 16vh;
  padding: 1rem 2.5rem;
  width: 100%;
  height: 85vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url('./assets/banner_shape.png');
  background-size: cover;
  /* background-position: center top; */
  background-color: #205C6D;
  /* background-color: #274067; */
  /* background-image: linear-gradient(#274067 10%, #205C6D 10%) */
}

.student-homepage-container .student-home-banner .banner-content {
  width: 45%;
  height: 100%;
  padding: 2rem 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}

.student-homepage-container .student-home-banner .banner-content h1 {
  color: #fff;
  font-size: 42px;
  font-weight: bold;
  font-family: Roboto, sans-serif;
  text-shadow: 0px 1px 3px rgba(150, 150, 150, 1);
}

.student-homepage-container .student-home-banner .banner-content p {
  color: #fff;
  font-size: 18px;
  margin: 2rem 0;
}

.student-homepage-container .student-home-banner .banner-content img {
  position: absolute;
  top: -30%;
  left: 40%;
  /* z-index: -1; */
}

.student-homepage-container .student-home-banner .banner-content button {
    width: 30%;
    padding: 18px 30px 15px;
    font-size: 12px;
    font-weight: 500;
    text-transform: uppercase;
    color: #fff;
    background-color: #56b8a1;
    border-style: solid;
    border-width: 2px;
    border-color: #fff;
    border-radius: 36px;
    outline: none;
}

.student-homepage-container .student-home-banner .banner-image {
  width: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
  /* background-color: white; */
}

.student-homepage-container .student-home-banner .banner-image .img-bg {
  position: absolute;
  top: 20%;
  left: 20%;
  z-index: 0;
  transform: scale(1.2);
}

.student-homepage-container .student-home-banner .banner-image .img-lines{
  position: absolute;
  top: 0%;
  left: 0%;
  z-index: 1;
}

.student-homepage-container .student-home-banner .banner-image .img-bulb{
  position: absolute;
  top: 35%;
  left: 3%;
  z-index: 2;
  animation: scaling 1.5s infinite alternate;
}

@keyframes scaling {
  From {
      transform: scale(0.6);
  }
  To {
      transform: rotateZ(45deg) scale(1);
  }
}

.student-homepage-container .student-home-banner .banner-image .img-people{
  position: absolute;
  top: 30%;
  left: 17%;
  z-index: 2;
}

.student-homepage-container .student-home-banner .banner-image .img-book-1{
  position: absolute;
  top: 0%;
  left: 25%;
  z-index: 4;
  transform: scale(0.7);
}

.student-homepage-container .student-home-banner .banner-image .img-book-2{
  position: absolute;
  top: 0%;
  left:35%;
  z-index: 3;
  transform: scale(0.7);
}

.student-homepage-container .student-home-banner .banner-image .img-compass{
  position: absolute;
  bottom: 0%;
  right: 0%;
  z-index: 3;
}

.student-homepage-container .student-home-banner .banner-image .img-cloud-1{
  position: absolute;
  top:6%;
  right: 0%;
  z-index: 3;
  animation:linear infinite alternate;
  animation-name: run;
  animation-duration: 15s;
}

@keyframes run {
  0% { right: 0;}
  50%{ right : 10%;}
  100%{ right: 0;}
}

.student-homepage-container .student-home-banner .banner-image .img-cloud-2{
  position: absolute;
  top: 22%;
  right: 0%;
  z-index: 3;
  animation:linear infinite alternate;
  animation-name: run2;
  animation-duration: 3s;
}

@keyframes run2 {
  0% { right: 0;}
  50%{ right : 10%;}
  100%{ right: 0;}
}


.student-homepage-container .personal-learning-container {
  padding-top: 10rem;
  margin: 2rem 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  background-color: #E9F6EF;
    background-size: contain;
    background-repeat: repeat;
  background-image: url("./assets/download.png");
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-header {
  width: 95%;
  position: absolute;
  top: 5%;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-header
  .personal-learning-text {
  width: 30%;
  display: flex;
  flex-direction: column;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-header
  .personal-learning-text
  p {
  font-size: 2.5rem;
  font-weight: bold;
  color: #00b255;
  margin-bottom: 10px;
  text-shadow: 0px 2px 3px rgba(150, 150, 150, 0.5);
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-header
  .personal-learning-text
  .text-dash {
  width: 80px;
  height: 5px;
  background-color: #00b255;
  border-radius: 10px;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-body {
  width: 100%;
  z-index: 3;
  padding-left: 5rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-body
  .subject-cards {
  width: 20vw;
  height: 300px;
  background-color: #fff;
  border-radius: 10px;
  padding-top: 70px;
  margin: 0 2.5rem 3rem 0;
  /* display: flex; */
  flex-direction: column;
  position: relative;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-body
  .subject-cards
  .card-head {
  width: 85%;
  height: 50px;
  border-radius: 0 50px 50px 0;
  position: absolute;
  padding: 0 1rem;
  top: 5%;
  left: -2.5%;
  display: flex;
  align-items: center;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-body
  .subject-cards
  .card-head
  p {
  color: #fff;
  font-size: 18px;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-body
  .subject-cards
  .card-body {
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-body
  .subject-cards
  .card-foot {
  width: 100%;
  height: 20px;
  display: flex;
  align-items: center;
  padding: 0 1rem;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-body
  .subject-cards
  .card-body
  .subject-item {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 10px;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-body
  .subject-cards
  .card-body
  .subject-item
  .chap-name {
  width: 70%;
}

.student-homepage-container
  .personal-learning-container
  .personal-learning-body
  .subject-cards
  .card-body
  .subject-item
  .skill {
  width: 30%;
  display: flex;
  justify-content: flex-end;
}

