.sidebar-cont {
	display: flex;
	width: 100%;
	height: 100%;
	transition: width 1s, height 1s;
}
.sidebar-cont .sidebar {
	margin: 0;
	padding: 0;
	width: 20%;
	background-color: #f1f1f1;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
}
.sidebar-cont .content {
	padding: 1rem;
	width: 80%;
	height: 100%;
	background-color: #fff;
}

.sidebar-cont .content .attendance-view {
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
}

.sidebar-cont .content .attendance-view .calender {
	width: 65%;
}

.sidebar-cont .content .behaviour-view {
	width: 100%;
	height: 100%;
}

.sidebar-cont .content .behaviour-view .point-stat {
	width: 100%;
	display: flex;
	align-items: center;
	margin-bottom: 1rem;
}

.sidebar-cont .content .behaviour-view .point-stat img {
	height: 20px;
	margin-right: 10px;
}

.sidebar-cont .content .behaviour-view .point-stat p {
	margin-right: 10px;
	font-weight: bold;
	color: #464646;
}

.sidebar-cont .content .behaviour-view .point-stat span {
	margin-right: 10px;
	font-weight: bold;
	color: #000;
}

.sidebar-cont .content .attendance-view .react-calendar {
	width: 100%;
	height: 100%;
}

.sidebar-cont .sidebar .creativity .icon i {
	color: rgb(225, 0, 0);
	font-size: 25px;
}
.sidebar-cont .sidebar .creativity .icon-name p {
	color: rgb(225, 0, 0);
}
.sidebar-cont .sidebar .Entrepreneurship .icon i {
	color: rgb(255, 127, 0);
	font-size: 25px;
}
.sidebar-cont .sidebar .Entrepreneurship .icon-name p {
	color: rgb(255, 127, 0);
}
.sidebar-cont .sidebar .ProblemSolving .icon i {
	color: rgb(244, 208, 63);
	font-size: 25px;
}
.sidebar-cont .sidebar .ProblemSolving .icon-name p {
	color: rgb(244, 208, 63);
}
.sidebar-cont .sidebar .Assignments .icon i {
	color: rgb(46, 204, 113);
	font-size: 25px;
}
.sidebar-cont .sidebar .Assignments .icon-name p {
	color: rgb(46, 204, 113);
}
.sidebar-cont .sidebar .Practices .icon i {
	color: rgb(52, 152, 219);
	font-size: 25px;
}
.sidebar-cont .sidebar .Practices .icon-name p {
	color: rgb(52, 152, 219);
}
.sidebar-cont .sidebar .Attendance .icon i {
	color: rgb(91, 44, 111);
	font-size: 25px;
}
.sidebar-cont .sidebar .Attendance .icon-name p {
	color: rgb(91, 44, 111);
}
.sidebar-cont .sidebar .Behaviour .icon i {
	color: rgb(91, 44, 111);
	font-size: 25px;
}
.sidebar-cont .sidebar .Behaviour .icon-name p {
	color: rgb(91, 44, 111);
}
.sidebar-cont .sidebar .creativity:hover .icon i {
	color: white;
	font-size: 25px;
}
.sidebar-cont .sidebar .creativity:hover .icon-name p {
	color: white;
}
.sidebar-cont .sidebar .Entrepreneurship:hover .icon i {
	color: white;
	font-size: 25px;
}
.sidebar-cont .sidebar .Entrepreneurship:hover .icon-name p {
	color: white;
}
.sidebar-cont .sidebar .ProblemSolving:hover .icon i {
	color: white;
	font-size: 25px;
}
.sidebar-cont .sidebar .ProblemSolving:hover .icon-name p {
	color: white;
}
.sidebar-cont .sidebar .Assignments:hover .icon i {
	color: white;
	font-size: 25px;
}
.sidebar-cont .sidebar .Assignments:hover .icon-name p {
	color: white;
}
.sidebar-cont .sidebar .Practices:hover .icon i {
	color: white;
	font-size: 25px;
}
.sidebar-cont .sidebar .Practices:hover .icon-name p {
	color: white;
}
.sidebar-cont .sidebar .Attendance:hover .icon i {
	color: white;
	font-size: 25px;
}
.sidebar-cont .sidebar .Attendance:hover .icon-name p {
	color: white;
}
.sidebar-cont .sidebar .Behaviour:hover .icon i {
	color: white;
	font-size: 25px;
}
.sidebar-cont .sidebar .Behaviour:hover .icon-name p {
	color: white;
}
.sidebar-cont .sidebar .creativity .icon,
.sidebar-cont .sidebar .Entrepreneurship .icon,
.sidebar-cont .sidebar .ProblemSolving .icon,
.sidebar-cont .sidebar .Assignments .icon,
.sidebar-cont .sidebar .Practices .icon,
.sidebar-cont .sidebar .Attendance .icon,
.sidebar-cont .sidebar .Behaviour .icon {
	width: 30%;
	display: flex;
	justify-content: center;
}
.sidebar-cont .sidebar .creativity .icon-name,
.sidebar-cont .sidebar .Entrepreneurship .icon-name,
.sidebar-cont .sidebar .ProblemSolving .icon-name,
.sidebar-cont .sidebar .Assignments .icon-name,
.sidebar-cont .sidebar .Practices .icon-name,
.sidebar-cont .sidebar .Attendance .icon-name,
.sidebar-cont .sidebar .Behaviour .icon-name {
	width: 70%;
	display: flex;
}
.sidebar-cont .sidebar p {
	display: block;
	color: black;
	padding: 16px;
	text-decoration: none;
}

/* .sidebar-cont .sidebar .creativity {
  background-color: rgb(197, 40, 40);
  width: 100%;
} */

.sidebar-cont .sidebar .creativity,
.sidebar-cont .sidebar .Entrepreneurship,
.sidebar-cont .sidebar .ProblemSolving,
.sidebar-cont .sidebar .Assignments,
.sidebar-cont .sidebar .Practices,
.sidebar-cont .sidebar .Attendance,
.sidebar-cont .sidebar .Behaviour {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	cursor: pointer;
}
.sidebar-cont .sidebar .creativity:hover,
.sidebar-cont .sidebar .Entrepreneurship:hover,
.sidebar-cont .sidebar .ProblemSolving:hover,
.sidebar-cont .sidebar .Assignments:hover,
.sidebar-cont .sidebar .Practices:hover,
.sidebar-cont .sidebar .Attendance:hover,
.sidebar-cont .sidebar .Behaviour:hover {
	background-color: black;
	color: #f1f1f1;
}

.sidebar-cont .sidebar .creativity.active,
.sidebar-cont .sidebar .Entrepreneurship.active,
.sidebar-cont .sidebar .ProblemSolving.active,
.sidebar-cont .sidebar .Assignments.active,
.sidebar-cont .sidebar .Practices.active,
.sidebar-cont .sidebar .Attendance.active,
.sidebar-cont .sidebar .Behaviour.active {
	background-color: black;
	color: #f1f1f1;
}

.sidebar-cont .sidebar .creativity.active .icon-name p,
.sidebar-cont .sidebar .creativity.active .icon i,
.sidebar-cont .sidebar .Entrepreneurship.active .icon-name p,
.sidebar-cont .sidebar .Entrepreneurship.active .icon i,
.sidebar-cont .sidebar .ProblemSolving.active .icon-name p,
.sidebar-cont .sidebar .ProblemSolving.active .icon i,
.sidebar-cont .sidebar .Assignments.active .icon-name p,
.sidebar-cont .sidebar .Assignments.active .icon i,
.sidebar-cont .sidebar .Practices.active .icon-name p,
.sidebar-cont .sidebar .Practices.active .icon i,
.sidebar-cont .sidebar .Attendance.active .icon-name p,
.sidebar-cont .sidebar .Attendance.active .icon i,
.sidebar-cont .sidebar .Behaviour.active .icon-name p .sidebar-cont .sidebar .Behaviour.active .icon i {
	color: #f1f1f1;
}

@media screen and (max-width: 700px) {
	.sidebar-cont .sidebar {
		width: 100%;
		height: auto;
		position: relative;
	}
	.sidebar-cont .sidebar p {
		float: left;
	}
	.content {
		margin-left: 0;
	}
}

@media screen and (max-width: 400px) {
	.sidebar p {
		text-align: center;
		float: none;
	}
}
