.gallery-container {
    width: 100% ;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.gallery-container .header{
    width: 100% !important;
    height: 15% !important;
    padding: 1rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
    border-bottom: 1px solid #c5c5c5;
    border-radius: 5px 5px 0 0;
}

.gallery-container .header .left{
    display: flex;
    flex-direction: column;
}

.gallery-container .header .left h1{
    font-size: 22px;
    color: #000;
}

.gallery-container .header .left h1 i{
    margin-right: 0.5rem;
    color: #1d8cf8;
    cursor: pointer;
}

.gallery-container .header .left p{
    font-size: 14px;
    color: #999999;
}

.gallery-container .header .left p span {
    color: #414141;
    font-weight: 500;
    font-size: 16px;
}


.gallery-container .header .right{
    display: flex;
    flex-direction: column;
    height: 100%;
    margin-right: 1.5rem;
}

.gallery-container .header .right .add-button{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: linear-gradient(90deg, #3358f4, #1d8cf8);
    width: 200px;
    height: 38px;
    border-radius: 6px;
    cursor: pointer;
    padding:0 1.2rem;
}

.gallery-container .header .right .add-button img{
    width: 20px;
    height: 20px;
}

.gallery-container .header .right .add-button p{
    font-size: 1rem;
    color: #fff;
}

.gallery-container .header .right .add-button:hover {
    box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.75);
}

.gallery-container .body{
    width: 100% !important;
    height: 85% !important;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
}

.gallery-container .body .gallery-list{
    width: 100% !important;
    height: 50px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
}

.gallery-container .body .gallery-list .gallery-box{
    width: 300px;
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border-radius: 6px;
    box-shadow: 0 1px 6px 0 #dee0e2;
    margin: 0.5rem;
    cursor: pointer;
    transform: translateY(0px);
    transition: all .3s ease-in-out;
}

.gallery-container .body .gallery-list .gallery-box img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 6px;
 }

 .gallery-container .body .gallery-list .gallery-box:hover{
     transform: translateY(-5px);
     transition: all .3s ease-in-out;
 }

 
.image-picker2{
    width: 100%;
    height: 250px;
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #e5e5e5;
    border-radius: 5px;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.15);
}

.image-picker2 input{
    width: 100%;
    height: 100%;
    opacity: 0;
    position: absolute;
    top: 0;
    cursor: pointer;
}
.image-picker2 img{
    width: 100%;
    height: 100%;
    border-radius: 5px;
    object-fit: cover;
    cursor: pointer;
}

.image-picker2 .image-icon{
    width: 50px;
    height: 50px;
    border-radius: 0;
}
