.my-abilities-container{
    width: 100%;
    min-height: 80vh;
    padding: 3.5rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.my-abilities-container .abilities-wrapper{
    width: 50%;
    padding: 1rem 0;
    background-color: #f5f7fa;
    border-radius: 6px;
    box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.my-abilities-container .abilities-wrapper .abilities-tab {
    width: 100%;
    padding: 0 1rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.my-abilities-container .abilities-wrapper .abilities-tab .tab {
    width: 33%;
    height: 100%;
    padding: 0.5rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border-radius: 5px;
}

.my-abilities-container .abilities-wrapper .abilities-tab .tab p {
    font-size: 14px;
    color: #a1a1a1;
}

.my-abilities-container .abilities-wrapper .abilities-tab .tab:hover {
    background-color: rgba(255, 255, 0, 0.35);
}

.my-abilities-container .abilities-wrapper .abilities-tab .tab.active {
    background-color: rgba(255, 255, 0, 0.35);
}

.my-abilities-container .abilities-wrapper .abilities-tab .tab img {
    height: 50px;
}

.my-abilities-container .abilities-wrapper .abilities-body {
    width: 100%;
    padding: 1rem;
    display: flex;
}