.questionLogs-container {
    width: 80%;
    padding: 2rem 0;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.15);
    display: flex;
    justify-content: center;
}

.questionLogs-container .questionLogs-board{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.questionLogs-container .questionLogs-board .questionLogs-head {
    width: 100%;
    padding: 0 1rem;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
}

.questionLogs-container .questionLogs-board .questionLogs-head h1 {
    font-size: 1.5rem;
    color: #555555;
    margin: 0;
}

.questionLogs-container .questionLogs-board .questionLogs-head p {
    margin: 0.5rem 0;
    color: #5b8a9b;
}

.questionLogs-container .questionLogs-board .questionLogs-body {
    width: 100%;
    padding:1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.questionLogs-container .questionLogs-board .questionLogs-body .questionLogs-table-body {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.questionLogs-container .questionLogs-board .questionLogs-body .questionLogs-table-body .questionLogs-accordion {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 1px;
}

.questionLogs-container .questionLogs-board .questionLogs-body .questionLogs-table-body .questionLogs-accordion .accordion {
    background-color: #eee;
    color: #444;
    cursor: pointer;
    padding: 1rem 0.5rem;
    width: 100%;
    border: none;
    text-align: left;
    outline: none;
    font-size: 15px;
    transition: all 0.4s ease-in-out;
    display: flex;
    align-items: center;
}

.questionLogs-container .questionLogs-board .questionLogs-body .questionLogs-table-body .questionLogs-accordion .accordion h1{
    font-size: 1rem;
    color: #5b8a9b;
}

.questionLogs-container .questionLogs-board .questionLogs-body .questionLogs-table-body .questionLogs-accordion .accordion p{
    font-size: 13px;
    color: #5b8a9b;
}

.questionLogs-container .questionLogs-board .questionLogs-body .questionLogs-table-body .questionLogs-accordion .panel {
    display: none;
    background-color: white;
    overflow: hidden;
    flex-direction: column;
}

.questionLogs-container .questionLogs-board .questionLogs-body .questionLogs-table-body .questionLogs-accordion .panel .panel-topic {
    width: 100%;
    padding: 1rem 1.5rem;
    background-color: #f5f7fa;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1.5px;
    cursor: pointer;
}

.questionLogs-container .questionLogs-board .questionLogs-body .questionLogs-table-body .questionLogs-accordion .panel .panel-topic:hover {
    background-color: 	rgba(255,255,102,0.35);
}

.questionLogs-container .questionLogs-board .questionLogs-body .questionLogs-table-body .questionLogs-accordion .panel .panel-topic h1 {
    font-size: 18px;
    margin: 0;
    color: #616161;
}

.questionLogs-container .questionLogs-board .questionLogs-body .questionLogs-table-body .questionLogs-accordion .panel .panel-topic p {
    font-size: 13px; 
    color: #616161;
}   

 



