.iigp-nav-container {
    width: 100%;
    height: 10vh;
    padding: 0 3.5rem;
    position: fixed;
    top: 6vh;
    z-index: 999;
    /* box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.15); */
    background-color: #205C6D;
    display: flex;
    align-items: center;
    justify-content: center;
}

.iigp-nav-container .iigp-logo-wrapper {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
}

.iigp-nav-container .iigp-logo-wrapper img {
    height: 50%;
}

.iigp-nav-container .iigp-menu-wrapper {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
}

.iigp-nav-container .iigp-menu-wrapper .link-box {
    width: 25%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
}

.iigp-nav-container .iigp-menu-wrapper .link-box::before {
    content: '';
    position: absolute;
    width: 0;
    bottom: -5px;
    height: 4px;
    background-color: #0eb582;
}

.iigp-nav-container .iigp-menu-wrapper .link-box:hover::before {
    content: "";
  position: absolute;
  width: 70px;
  bottom: 0px;
  height: 4px;
  background-color: #0eb582;
  border-radius: 3px;
  transition: width 0.5s ease-in-out;
  transform-origin: left;
}

.iigp-nav-container .iigp-menu-wrapper
.link-box:hover p {
  color: #0eb582;
}

.iigp-nav-container .iigp-menu-wrapper .link-box:last-child:hover::before {
    content: '';
    position: absolute;
    width: 0px;
    bottom: 0px;
    height: 3px;
    background-color:#fff;
    border-radius: 3px;
    transition: width 0.5s ease-in-out;
    transform-origin: left;
}

.iigp-nav-container .iigp-menu-wrapper .link-box p {
    font-size: 14px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-family: Roboto, sans-serif;
}

.iigp-nav-container .iigp-menu-wrapper .link-box button {
    padding: 0.6rem 2rem;
    font-size: 12px;
    font-weight: 500;
    text-transform: uppercase;
    color: #fff;
    background-color: rgba(32,92,109,0.1);
    border-style: solid;
    border-width: 2px;
    border-color: #fff;
    border-radius: 40px;
    outline: none;
}

.iigp-nav-container .iigp-menu-wrapper .link-box button:hover {
    padding: 0.6rem 2rem;
    font-size: 12px;
    font-weight: 500;
    text-transform: uppercase;
    color: #fff;
    background-color: #56b8a1;
    border-style: solid;
    border-width: 2px;
    border-color: #fff;
    border-radius: 40px;
	outline: none;
}