.students-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.students-container .discussion-list-head {
    width: 100%;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    background-color: #fff;
    border-bottom: 1px solid #c5c5c5;
    border-radius: 5px 5px 0 0;
}

.students-container .discussion-list-head div {
    display: flex;
}

.students-container .discussion-list-head div p {
    margin-right: 10px;
    cursor: pointer;
}

.students-container .discussion-list-head div img {
    height: 30px;
}

.students-container .discussion-list-body {
    width: 100%;
    height: 100%;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
}

.students-container .discussion-list-body .discussion-user-section {
    width: 33%;
    border-right: 1px solid #d5d5d5;
    padding: 0 0.5rem;
}

.students-container .discussion-list-body .discussion-user-section:last-child {
    border: none;
}

.students-container .discussion-list-body .discussion-user-section .discussion-user-section-head {
    width: 100%;
    padding: 0.5rem;
    background-color: lemonchiffon;
}

.students-container .discussion-list-body .discussion-user-section .discussion-user-section-head p {
    font-size: 20px;
    color: #e99400;
}

.students-container .discussion-list-body .discussion-user-section .discussion-user-section-body {
    width: 100%;
    height: 68vh;
    padding: 1rem 0;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
}

.students-container .discussion-list-body .discussion-user-section .discussion-user-section-body .discussion-group-card {
    width: 100%;
    padding: 0.6rem;
    -webkit-box-shadow: 0px 0px 3px 0px rgba(0,0,0,0.10);
    -moz-box-shadow: 0px 0px 3px 0px rgba(0,0,0,0.10);
    box-shadow: 0px 0px 3px 0px rgba(0,0,0,0.10);
    cursor: pointer;
    border-radius: 5px;
    background-color: rgba(220, 236, 243, 0.25);
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
}

.students-container .discussion-list-body .discussion-user-section .discussion-user-section-body .discussion-group-card:hover {
    background-color: rgba(220, 236, 243, 1);
}

.students-container .discussion-list-body .discussion-user-section .discussion-user-section-body .discussion-group-card h3 {
    color: #3c8dbc;
    font-size: 18px;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.students-container .discussion-list-body .discussion-user-section .discussion-user-section-body .discussion-group-card h5 {
    font-size: 1rem;
}

.students-container .discussion-list-body .discussion-user-section .discussion-user-section-body .discussion-group-card h3 span {
    color: #bb3f3f;
}

.students-container .discussion-list-body .discussion-user-section .discussion-user-section-body .discussion-group-card p {
    color: #777777;
    font-size: 13px;
}

.students-container .discussion-list-body .discussion-user-section .discussion-user-section-body .discussion-group-card span {
    font-size: 12px;
    color: #a1a1a1;
}