.pollDetails-container {
	width: 100%;
	padding: 1rem 0;
	display: flex !important;
	align-items: center !important;
	justify-content: center !important;
}

.pollDetails-container .pollChart {
	width: 50% !important;
	height: 100% !important;
	display: flex !important;
	align-items: center !important;
	justify-content: center !important;
}

.pollDetails-container .pollTable {
	width: 50% !important;
	max-height: 50vh;
	overflow-y: scroll;
}

.poll-list {
	width: 100%;
	max-height: 65vh;
	margin-bottom: 1rem;
	overflow-y: scroll;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.poll-list .list-container {
	width: 100%;
	height: 80%;
	padding: 0.5rem;
	margin-bottom: 1rem;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.poll-list .list-container .addedPoll {
	width: 99%;
	padding: 0.8rem;
	margin-bottom: 10px;
	box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.25);
	display: flex;
	flex-direction: column;
	border-radius: 10px;
}

.poll-list .list-container .addedPoll div {
	display: flex;
}

.poll-list .list-container .addedPoll div p {
	margin: 1rem 1rem 0 0;
	padding: 0.5rem;
	border-radius: 5px;
}

.poll-list .add-poll-btn {
	width: 50%;
	height: 20%;
	display: flex;
	align-items: center;
	justify-content: center;
	border: 0.1rem dashed #325cf4;
	border-radius: 6px;
	cursor: pointer;
	padding: 0.5rem;
}

.poll-list .add-poll-btn h1 {
	margin: 0;
	font-size: 1rem;
	color: #325cf4;
}

.poll-list .add-poll-btn p {
	margin: 0 0.5rem;
	font-size: 1rem;
	color: #325cf4;
	font-weight: bold;
}

.poll-list .add-poll-btn img {
	height: 40px;
}

.pollDetails-container {
	display: flex;
	width: 100%;
	flex-direction: column;
	align-items: center;
}

.pollDetails-container .poll-holder {
	width: 100%;
	padding: 1rem;
	background-color: #f6f6f6;
	margin: 0 0 1rem 0;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
}

.pollDetails-container .poll-chart-data {
	width: 100%;
	display: flex;
}
