.student-practice-container {
  width: 100%;
}

.student-practice-container .learning-breadcrumb {
  width: 100%;
  margin-top: 16vh;
  display: flex;
  align-items: center;
  padding: 1rem 3.5rem;
  background: rgb(2, 196, 215);
  background: linear-gradient(
    90deg,
    rgba(2, 196, 215, 1) 0%,
    rgba(126, 226, 186, 1) 100%
  );
}

.student-practice-container .learning-breadcrumb p {
  color: #fff;
  font-size: 24px;
  font-weight: 500;
  text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.2);
}

.student-practice-container .learning-breadcrumb p span {
  margin: 0 1rem;
}

.student-practice-container .learning-choose-subject-breadcrumb {
  width: 100%;
  height: 80px;
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.75);
}

.student-practice-container .learning-grade-breadcrumb {
  width: 100%;
  background: #e9f6ef;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.15);
  display: flex;
  align-items: center;
  padding: 1rem 3.5rem;
}

.student-practice-container .learning-grade-breadcrumb p {
  color: #00b255;
  font-size: 18px;
  font-weight: bold;
  text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1);
}

.student-practice-container .learning-choose-subject-breadcrumb {
  width: 100%;
  height: 100px;
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.75);
  padding: 0 2rem;
  display: flex;
  align-items: center;
}

.student-practice-container
  .learning-choose-subject-breadcrumb
  .subject-option {
  width: 180px;
  height: 90px;
  padding: 0.5rem 0;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  cursor: pointer;
  border-radius: 15px;
  transition: all 0.3s ease-in-out;
}

.student-practice-container
  .learning-choose-subject-breadcrumb
  .subject-option:hover {
  background-color: #f5f7fa;
}

.student-practice-container
  .learning-choose-subject-breadcrumb
  .subject-option
  img {
  height: 40px;
}

.student-practice-container
  .learning-choose-subject-breadcrumb
  .subject-option
  p {
  font-size: 14px;
  font-weight: 500;
  color: #a1a1a1;
}

.student-practice-container .learning-grade-breadcrumb {
  width: 100%;
  background: #e9f6ef;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.15);
  display: flex;
  align-items: center;
  padding: 1rem 3.5rem;
}

.student-practice-container .learning-grade-breadcrumb p {
  color: #00b255;
  font-size: 18px;
  font-weight: bold;
  text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1);
}

.student-practice-container .learning-grade-breadcrumb p span {
  margin: 0 1rem;
}

.student-practice-container .learning-preview-container {
  width: 100%;
  height: 70vh;
  padding: 2rem;
  background-color: #E9F6EF;
    background-image: url('./assets/download.png');
    background-size: contain;
    background-repeat: repeat;
  display: flex;
  justify-content: center;
}

.student-practice-container .learning-preview-container .preview-box {
  width: 80%;
  height: 90%;
  background-color: #fff;
  border-radius: 20px;
  box-shadow: 0px 7px 50px 0px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.student-practice-container .learning-preview-container .preview-box h1 {
  font-weight: bolder;
  color: #00b255;
}

.student-practice-container
  .learning-preview-container
  .preview-box
  .skill-preview-head {
  width: 100%;
  height: 20%;
  padding: 0 2rem;
  border-radius: 20px 20px 0 0;
  display: flex;
  align-items: center;
}

.student-practice-container
  .learning-preview-container
  .preview-box
  .skill-preview-head
  p {
  font-size: 16px;
  letter-spacing: 1px;
  font-weight: bold;
}

.student-practice-container
  .learning-preview-container
  .preview-box
  .skill-preview-head
  .preview-step {
  width: 150px;
  height: 100%;
  margin: 0 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.student-practice-container
  .learning-preview-container
  .preview-box
  .skill-preview-head
  .preview-step
  .circle-step {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background-color: #eeeeee;
  border: none;
  position: relative;
  display: flex;
  align-items: center;
}

.student-practice-container
  .learning-preview-container
  .preview-box
  .skill-preview-head
  .preview-step
  .circle-step:first-child::before {
  display: none;
}

.student-practice-container
  .learning-preview-container
  .preview-box
  .skill-preview-head
  .preview-step
  .circle-step::before {
  content: "";
  position: absolute;
  right: 20px;
  width: 23.5px;
  height: 4px;
  background-color: #eeeeee;
}

.student-practice-container
  .learning-preview-container
  .preview-box
  .skill-preview-head
  .preview-step
  .circle-step.active {
  background-color: #04c5d7;
  position: relative;
}

@keyframes line {
  from {
    width: 0px;
  }
  to {
    width: 25px;
  }
}

.student-practice-container
  .learning-preview-container
  .preview-box
  .skill-preview-head
  .preview-step
  .circle-step::after {
  content: "";
  position: absolute;
  left: 20px;
  width: 25px;
  height: 4px;
  background-color: #eeeeee;
}

.student-practice-container
  .learning-preview-container
  .preview-box
  .skill-preview-head
  .preview-step
  .circle-step.active::after {
  content: "";
  position: absolute;
  left: 20px;
  width: 25px;
  height: 4px;
  background-color: #04c5d7;
  animation-name: line;
  animation-duration: 2s;
  /* animation-delay: 1s; */
  animation-timing-function: ease-in;
  z-index: 999;
}

.student-practice-container
  .learning-preview-container
  .preview-box
  .skill-preview-head
  .preview-step
  .circle-step:last-child::after {
  display: none;
}

.student-practice-container
  .learning-preview-container
  .preview-box
  .skill-preview-body {
  width: 100%;
  height: 60%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.student-practice-container
  .learning-preview-container
  .preview-box
  .skill-preview-body
  .skill-box {
  width: 90%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.student-practice-container
  .learning-preview-container
  .preview-box
  .skill-preview-body
  .skill-box
  .skill-question
  img {
  height: 150px;
  object-fit: contain;
  /* width: 200px; */
}

.student-practice-container
  .learning-preview-container
  .preview-box
  .skill-preview-body
  img {
  width: 90%;
  height: 100%;
}

.student-practice-container
  .learning-preview-container
  .preview-box
  .skill-preview-foot {
  width: 100%;
  height: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.student-practice-container
  .learning-preview-container
  .preview-box
  .skill-preview-foot
  button {
  border: none;
  border-radius: 5px;
  padding: 0.5rem 1rem;
  background-color: #04c5d7;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}
