.my-discussions-container {
    width: 100%;
    min-height: 80vh;
    padding: 3.5rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.my-discussions-container .discussions-wrapper {
    width: 70%;
    min-height: 50vh;
    padding: 1rem;
    background-color: #f5f7fa;
    border-radius: 6px;
    box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
}

.my-discussions-container .discussions-wrapper .my-discussion-card {
    width: 100%;
    padding: 0.6rem;
    -webkit-box-shadow: 0px 0px 3px 0px rgba(0,0,0,0.10);
    -moz-box-shadow: 0px 0px 3px 0px rgba(0,0,0,0.10);
    box-shadow: 0px 0px 3px 0px rgba(0,0,0,0.10);
    cursor: pointer;
    border-radius: 5px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
}


.my-discussions-container .discussions-wrapper .my-discussion-card h3 {
    color: #3c8dbc;
    font-size: 18px;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.my-discussions-container .discussions-wrapper .my-discussion-card h5 {
    font-size: 1rem;
}

.my-discussions-container .discussions-wrapper .my-discussion-card h3 span {
    color: #bb3f3f;
}

.my-discussions-container .discussions-wrapper .my-discussion-card p {
    color: #777777;
    font-size: 13px;
}

.my-discussions-container .discussions-wrapper .my-discussion-card span {
    font-size: 12px;
    color: #a1a1a1;
}