.my-homeworks-container {
    width: 100%;
    min-height: 80vh;
    /* padding: 1rem 0; */
    padding: 3.5rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.my-homeworks-container .homeworks-wrapper {
    width: 50%;
    padding: 1rem 0;
    background-color: #f5f7fa;
    border-radius: 6px;
    box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.my-homeworks-container .homeworks-wrapper .select-sub{
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 2rem;
}

.my-homeworks-container .homeworks-wrapper .homework-list {
    width: 100%;
    padding: 0 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.my-homeworks-container .homeworks-wrapper .homework-list .singleHomework-container {
    width: 100%;
    border-radius: 6px;
    border: 1px solid #c5c5c5;
    background-color: #fff;
    margin-bottom: 1rem;
    padding: 0 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.my-homeworks-container .homeworks-wrapper .homework-list .singleHomework-container img {
    height: 50px;
    width: 50px;
}

.my-homeworks-container .homeworks-wrapper .homework-list  .singleHomework-container .homework-head {
    width: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
}

.my-homeworks-container .homeworks-wrapper .homework-list  .singleHomework-container .homework-head p {
    font-size: 16px;
    font-weight: bold;
    color: #464c4f;
    margin-bottom: 0.5rem;
}

.my-homeworks-container .homeworks-wrapper .homework-list  .singleHomework-container .homework-head .objectives{
    font-weight: normal;
    font-size: 14px;
}

.my-homeworks-container .homeworks-wrapper .homework-list  .singleHomework-container .homework-head h5{
    font-weight: normal;
    font-size: 14px;
}

.my-homeworks-container .homeworks-wrapper .homework-list  .singleHomework-container .homework-head span {
    font-size: 12px;
    font-weight: bold;
    color: #717171;
    margin-bottom: 0.2rem;
}
