.singleAnnouncement-container {
    width: 100%;
    border-radius: 6px;
    border: 1px solid #c5c5c5;
    background-color: #fff;
    margin-bottom: 1rem;
    display: flex;
    flex-direction: column;
}

.singleAnnouncement-container .announcement-head {
    width: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
}

.singleAnnouncement-container .announcement-head button {
    width: 20%;
    background: #fff;
    background-image: linear-gradient(-180deg,#fff 0,#efefef);
    border-radius: 3px;
    border: 1px solid #d7dbdb;
    box-shadow: inset 0 -1px 0 0 #d7dbdb;
    color: #464c4f;
    padding: 0.5rem 0.8rem;
    font-size: 12px;
    outline: none;
    transition: all 0.3s ease-in-out;
    cursor: not-allowed;
}

.singleAnnouncement-container .announcement-head button:hover {
    background-image: linear-gradient(180deg,#fff 0,#efefef);
}

.singleAnnouncement-container .announcement-head p {
    font-size: 18px;
    font-weight: bold;
    color: #464c4f;
    margin-bottom: 1rem;
}

.singleAnnouncement-container .announcement-body {
    width: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.singleAnnouncement-container .announcement-body .teacher-info {
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.singleAnnouncement-container .announcement-body .teacher-info img {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.singleAnnouncement-container .announcement-body .teacher-info p {
    font-size: 14px;
    font-weight: bold;
}