.quizMaker-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.quizMaker-container .quizMaker-header {
    width: 90%;
    height: 100px;
    padding: 1rem 3.5rem;
    border-radius: 5px;
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.10);
    background-color: #fff;
    display: flex;
}

.input-field{
    width: 50px;
    height: 50px;
}

.quizMaker-container .quizMaker-header .quizMaker-header-left {
    width: 70%;
    height: 100%;
    display: flex;
}

.quizMaker-container .quizMaker-header .quizMaker-header-left p {
    margin-right: 10px;
    font-size: 16px;
    color: #009AE0;
}

.quizMaker-container .quizMaker-header .quizMaker-header-left div {
    height: 100%;
    width: 80%;
    display: flex;
    flex-direction: column;
}

.quizMaker-container .quizMaker-header .quizMaker-header-left div h1 {
    margin: 0;
    font-size: 18px;
    color: #333333;
}

.quizMaker-container .quizMaker-header .quizMaker-header-left div p {
    margin: 5px 0;
    font-size: 14px;
    color: #333333;
}

.quizMaker-container .quizMaker-header .quizMaker-header-right {
    width: 30%;
    height: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
}

.add-option-button {
    padding: 0.5rem;
    border: 3.5px dotted #0090EA;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2rem 0;
    cursor: pointer;
    color: #0090EA;
    font-weight: 500;
}

.quizMaker-container .quizMaker-body {
    width: 90%;
    height: 100%;
    margin: 1rem 0 0 0;
    overflow-y: scroll;
    /* display: flex;
    flex-direction: column;
    align-items: center; */
}

.quizMaker-container .quizMaker-body .level-box {
    width: 100%;
    height: 70px;
    border-radius: 5px;
    background-color: #fff;
    border: 2px solid #0090EA;
    display: flex;
    flex-direction: column;
    transition: height 0.3s ease-in-out;
    margin-bottom: 10px;
}

.quizMaker-container .quizMaker-body .level-box:last-child {
    margin: 0;
}

.quizMaker-container .quizMaker-body .level-box.active:last-child {
    margin: 0;
}

.quizMaker-container .quizMaker-body .level-box.active {
    height:55vh;
    transition: height 0.3s ease-in-out;
    overflow-y: scroll;
    margin-bottom: 10px;
}

.quizMaker-container .quizMaker-body .level-box .level-box-header {
    width: 100%;
    padding: 0.5rem 1rem;
    display: flex;
    flex-direction: column;
}

.quizMaker-container .quizMaker-body .level-box .level-box-header div {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.quizMaker-container .quizMaker-body .level-box .level-box-header div h1 {
    font-size: 18px;
    margin: 0;
}

.quizMaker-container .quizMaker-body .level-box .level-box-header div p {
    font-size: 20px;
    font-weight: 500;
    color: #0090EA;
    cursor: pointer;
}

.quizMaker-container .quizMaker-body .level-box .level-box-header p {
    font-size: 13px;
    color: #9b9b9b;
    font-weight: 500;
    color: red;
    cursor: pointer;
}

.quizMaker-container .quizMaker-body .level-box .level-box-header p:hover {
    text-decoration: underline;
}

.quizMaker-container .quizMaker-body .level-box .level-box-body {
    width: 100%;
    height: 0px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    transition: height 0.35s ease-in-out;
}

.quizMaker-container .quizMaker-body .level-box.active .level-box-body {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    transition: height 0.35s ease-in-out;
    overflow-y: scroll;
}

.quizMaker-container .quizMaker-body .level-box .level-box-body .ques-box{
    width: 100%;
    padding: 1.5rem 1rem;
    border-top: 1px solid #9b9b9b;
    display: flex;
}

.quizMaker-container .quizMaker-body .level-box .level-box-body .ques-box .ques-index{
    width: 5%;
    height: 100%;
}

.quizMaker-container .quizMaker-body .level-box .level-box-body .ques-box .ques-body{
    width: 95%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.quizMaker-container .quizMaker-body .level-box .level-box-body .ques-box .ques-body .ques-body-ques {
    width: 100%;
    margin-bottom: 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.quizMaker-container .quizMaker-body .level-box .level-box-body .ques-box .ques-body .ques-body-ques span {
    font-size: 13px;
    color: #616161;
}

.quizMaker-container .quizMaker-body .level-box .level-box-body .ques-box .ques-body .ques-body-ques img{
    width: 200px;
    object-fit: contain;
}

.quizMaker-container .quizMaker-body .level-box .level-box-body .ques-box .ques-body .ques-body-options {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.quizMaker-container .quizMaker-body .level-box .level-box-body .ques-box .ques-body .ques-body-options .r-in {
    margin-bottom: 20px;
}

.quizMaker-container .quizMaker-body .level-box .level-box-body .ques-box .ques-body .ques-body-marks {
    width: 100%;
}

.quizMaker-container .quizMaker-body .level-box .level-box-body .ques-box .ques-body .ques-body-marks div {
    width: 40px;
    border-radius: 5px;
    background-color: #E4FFE8;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.quizMaker-container .quizMaker-body .level-box .level-box-body .ques-box .ques-body .ques-body-marks div p {
    color: #52B08D;
    font-weight: 500;
}

.errect {
    transform: rotate(0deg);
    transition: all 0.3s ease-in-out;
}

.rotate { 
    transform: rotate(180deg);
    transition: all 0.3s ease-in-out;
}

.addQuestionModal {
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,0.30);
    position: absolute;
    top: 0;
    right: 0;
    z-index: 999;
    overflow-y: scroll;
}

.addQuestionModal .addModal-container {
    width: 75%;
    margin: 1rem 0;
    background-color: #fff;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    margin:1rem auto;
}

.addQuestionModal .addModal-container .addModal-header {
    width: 100%;
    padding: 1rem 1.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #e1e1e1;
}

.addQuestionModal .addModal-container .addModal-header h1 {
    font-size: 18px;
    margin: 0;
}

.addQuestionModal .addModal-container .addModal-header .addModal-header-buttons {
    width: 10%;
    display: flex;
    justify-content: space-between;
}

.addQuestionModal .addModal-container .addModal-header .addModal-header-buttons p {
    cursor: pointer;
    color: #d1d1d1;
    font-size: 20px;
    margin: 0;
}

.addQuestionModal .addModal-container .addModal-header .addModal-header-buttons button {
    background-color: #fff;
    padding: 0.1rem 0.8rem;
    border-radius: 5px;
    color: #0090EA;
    border: 1.5px solid #0090EA;
}

.addQuestionModal .addModal-container .addModal-header .addModal-header-buttons button:hover {
    color: #fff;
    background-color: #0090EA;
}

.addQuestionModal .addModal-container .addModal-body {
    width: 100%;
    padding: 1rem 0.5rem;
}

.ck-content {
    border: 1px solid #d1d1d1 !important;
    border-radius: 5px !important;
}

.ck-content .image img{
    width: 200px;
    object-fit: contain;
}