.student-footer-container {
    width: 100%;
    background-color: #273C66;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.student-footer-container .footer-links {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.student-footer-container .footer-links .links-column {
    width: 23%;
    height: 100%;
    padding: 3rem 0;
    display: flex;
    flex-direction: column;
}

.student-footer-container .footer-links .links-column h1 {
    font-size: 18px;
    color: #fff;
    font-weight: bold;
    font-family: Roboto, sans-serif;
    margin-bottom: 1rem;
}

.student-footer-container .footer-links .links-column img {
    height: 30px;
    width: 80px;
    margin-bottom: 1rem;
}

.student-footer-container .footer-links .links-column p {
    color: #a5b7d9;
    margin-bottom: 0.6rem;
}

.student-footer-container .footer-links .links-column span {
    color: #a5b7d9;
    font-size: 13px;
}

.student-footer-container .footer-copyright {
    width: 70%;
    height: 15vh;
    border-top: 1px solid #a5b7d9;
    /* position: absolute;
    bottom: 0%; */
    display: flex;
    align-items: center;
    justify-content: center;
}

.student-footer-container .footer-copyright p{
    color: #fff;
}