.my-assignment-grade-container {
	width: 100%;
	display: flex;
	flex-direction: column;
}
.my-assignment-grade-container .graph {
	width: 500px;
	height: 400px;
	margin: 0 auto;
}

.my-assignment-grade-container .select-sub {
	width: 100%;
	display: flex;
	align-items: center;
}

.my-assignment-grade-container .score-body {
	width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.my-assignment-grade-container .score-body .score-table-head {
    width: 100%;
    height: 65px;
    background-color: #00AEEF;
    display: flex;
}

.my-assignment-grade-container .score-body .score-table-head .table-head-left {
    width: 50%;
    height: 100%;
    padding: 0 0.5rem;
    display: flex;
    align-items: center;
}

.my-assignment-grade-container .score-body .score-table-head .table-head-left p {
    color: #fff;
    font-weight: 500;
    font-size: 13px; 
}

.my-assignment-grade-container .score-body .score-table-head .table-head-right {
    width: 50%;
    height: 100%;
    padding: 0 0.5rem;
    display: flex;
    align-items: center;
}

.my-assignment-grade-container .score-body .score-table-head .table-head-right .marks{
    width: 20%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-right: 1px solid #d5d5d5;
    border-left: 1px solid #d5d5d5;
}

.my-assignment-grade-container .score-body .score-table-head .table-head-right .marks p {
    color: #fff;
    font-weight: 500;
    font-size: 13px;   
}

.my-assignment-grade-container .score-body .score-table-head .table-head-right .remarks{
    width: 80%;
	height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.my-assignment-grade-container .score-body .score-table-head .table-head-right .remarks p {
    color: #fff;
    font-weight: 500;  
    font-size: 13px;  
}

.my-assignment-grade-container .score-body .score-table-body {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.my-assignment-grade-container .score-body .score-table-body .stat-panel {
    width: 100%;
    display: flex;
    align-items: center;
}

.my-assignment-grade-container .score-body .score-table-body .stat-panel:hover {
    background-color: 	rgba(255,255,102,0.35);
}

.my-assignment-grade-container .score-body .score-table-body .stat-panel .panel-left {
    width: 50%;
    height: 40px;
    display: flex;
    align-items: center;
    padding: 0rem 1.5rem;
}

.my-assignment-grade-container .score-body .score-table-body .stat-panel .panel-left p {
    font-size: 13px;
    color: #616161;
}

.my-assignment-grade-container .score-body .score-table-body .stat-panel .panel-right {
    width: 50%;
    height: 40px;
    padding: 0 0 0 0.5rem;
    display: flex;
    align-items: center;
}

.my-assignment-grade-container .score-body .score-table-body .stat-panel .panel-right .marks{
    width: 20%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-left: 1px solid #d5d5d5;
    border-right: 1px solid #d5d5d5;
    border-bottom: 1px solid #d5d5d5;
}

.my-assignment-grade-container .score-body .score-table-body .stat-panel .panel-right .marks p {
    font-size: 13px; 
    color: #616161;  
}

.my-assignment-grade-container .score-body .score-table-body .stat-panel .panel-right .remarks{
    width: 80%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid #d5d5d5;
    background-color: #fafafa;
}

.my-assignment-grade-container .score-body .score-table-body .stat-panel .panel-right .remarks p {

    font-size: 13px; 
    color: #616161;  
}

