.singlePoll-container {
    width: 100%;
    border-radius: 6px;
    border: 1px solid #c5c5c5;
    background-color: #fff;
    margin-bottom: 1rem;
    display: flex;
    flex-direction: column;
}

.singlePoll-container .poll-head {
    width: 100%;
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.singlePoll-container .poll-head button {
    background: #fff;
    background-image: linear-gradient(-180deg,#fff 0,#efefef);
    border-radius: 3px;
    border: 1px solid #d7dbdb;
    box-shadow: inset 0 -1px 0 0 #d7dbdb;
    color: #464c4f;
    padding: 0.5rem 0.8rem;
    font-size: 12px;
    outline: none;
    transition: all 0.3s ease-in-out;
}

.singlePoll-container .poll-head button:hover {
    background-image: linear-gradient(180deg,#fff 0,#efefef);
}

.singlePoll-container .poll-head p {
    font-size: 18px;
    font-weight: bold;
    color: #464c4f;
}

.singlePoll-container .poll-body {
    width: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.singlePoll-container .poll-body .teacher-info {
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.singlePoll-container .poll-body .teacher-info img {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.singlePoll-container .poll-body .teacher-info p {
    font-size: 14px;
    font-weight: bold;
}

.singlePoll-container .poll-body .poll-questions {
    width: 100%;
    background-color: red !important;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.poll-submit-wrapper {
    width: 100%;
    height: 50vh;
    clip-path: circle(10% at 0 0);
    background-color: #f5f7fa;
    display: flex;
    align-items: center;
    justify-content: center;
    animation: circular 4s ease-in-out;
    /* transition: clip-path 0.5s ease-in-out; */
}

@keyframes circular {
    0%{clip-path: circle(10% at 0 0);}
    50%{clip-path: circle(142.2% at 0 0);}
    100%{clip-path: circle(10% at 100% 0);}
}
