.chooseChapter-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
}

.chooseChapter-container .chapters-cardFolder-container {
    width: 250px;
    height: 150px;
    margin: 0 50px 50px 0;
    background: #fff;
    border-radius: .4rem;
    box-shadow: 0 0.1rem 0.4rem rgba(0,0,0,.12);
    display: flex;
    flex-direction: column;
    cursor: pointer;
    transition: all 0.3s;
}

.chooseChapter-container .chapters-cardFolder-container:hover .chapters-cardFolder-img img {
    margin-bottom: 10px;
    box-shadow: 0px 5px 5px 0px rgba(0,0,0,0.2);
}

.chooseChapter-container .chapters-cardFolder-container .chapters-cardFolder-img{
    width: 100%;
    height: 70%;
    border-bottom: 1px solid #d3d3d3;
    display: flex;
    align-items: center;
    justify-content: center;
}

.chooseChapter-container .chapters-cardFolder-container .chapters-cardFolder-img img {
    transition: all 0.3s;
}

.chooseChapter-container .chapters-cardFolder-container .chapters-cardFolder-img:last-child img {
    box-shadow: none;
}

.chooseChapter-container .chapters-cardFolder-container .chapters-cardFolder-name{
    width: 100%;
    height: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.chooseChapter-container .chapters-cardFolder-container .chapters-cardFolder-name p {
    font-size: 14px;
    color: #333333;
}