.classes-container {
    width: 100% ;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.classes-container .header-section{
    width: 100% !important;
    height: 15% !important;
    padding: 1rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
    border-bottom: 1px solid #c5c5c5;
    border-radius: 5px 5px 0 0;
}

.classes-container .header-section .left{
    display: flex;
    flex-direction: column;
}

.classes-container .header-section .left h1{
    font-size: 22px;
    color: #000;
}

.classes-container .header-section .left h1 i{
    margin-right: 0.5rem;
    color: #1d8cf8;
    cursor: pointer;
}

.classes-container .header-section .left p{
    font-size: 14px;
    color: #999999;
}

.classes-container .header-section .left p span {
    color: #414141;
    font-weight: 500;
    font-size: 16px;
}


.classes-container .header-section .right{
    display: flex;
    flex-direction: column;
    height: 100%;
    margin-right: 1.5rem;
}

.classes-container .header-section .right .add-button{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: linear-gradient(90deg, #3358f4, #1d8cf8);
    width: 200px;
    height: 38px;
    border-radius: 6px;
    cursor: pointer;
    padding:0 1.2rem;
}

.classes-container .header-section .right .add-button img{
    width: 20px;
    height: 20px;
}

.classes-container .header-section .right .add-button p{
    font-size: 1rem;
    color: #fff;
}

.classes-container .header-section .right .add-button:hover {
    box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.75);
}

.classes-container .body{
    width: 100% !important;
    height: 85% !important;
    display: flex;
    flex-direction: column;
}

.classes-container .body .class-list{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    overflow-x: scroll;
}

.classes-container .body .class-list .cycle{
    width: 300px;
    height: 480px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    margin:1rem;
}

.classes-container .body .class-list .cycle:nth-child(1){
    background: linear-gradient(25deg, #1e3d6f, #c7cbd9);
}
.classes-container .body .class-list .cycle:nth-child(2){
    background: linear-gradient(25deg, #1E8859, #c7cbd9);
}
.classes-container .body .class-list .cycle:nth-child(3){
    background: linear-gradient(25deg, #0D90C3, #c7cbd9);
}

.classes-container .body .class-list .cycle-section{
    background: linear-gradient(25deg, #0D90C3, #c7cbd9) !important;
}



.classes-container .body .class-list .cycle .cycle-header
{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: #fbfbfb;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1rem 0;
}

.classes-container .body .class-list .cycle .cycle-header h1{
    font-size: 2.5rem;
    color: #464646;
}

.classes-container .body .class-list .cycle .sub-header{
    font-size: 1.8rem;
    font-weight: 500;
    color: #fff;
}

.classes-container .body .class-list .cycle .cycle-class-list{
    width: 100%;
    height: 65%;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding:0 1rem;
}

.classes-container .body .class-list .cycle .cycle-class-list .class-box{
    margin: 1rem 0 0 0;
}

.classes-container .body .class-list .cycle .cycle-class-list .class-box p{
    font-size: 1.2rem;
    font-weight: 500;
    color: #fff;
    cursor: pointer;
    transform: translateY(0px);
    transition: transform 0.35s ease-in-out;
}

.classes-container .body .class-list .cycle .cycle-class-list .class-box p:hover{
    transform: translateY(-5px);
    transition: transform 0.35s ease-in-out;
}

.classes-container .body .class-list .cycle-section .cycle-class-list .class-box p{
    cursor: default;
    transform: translateY(0px);
}
.container-group-close{
     display: flex;
     align-items: center;
     flex-wrap: wrap;
     flex-direction: row;
 } 
 .less-margin{
     margin: 0.25rem !important;
 }