.studentCard-container {
	width: 260px;
	height: 350px;
	margin: 1rem;
	box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.25);
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.studentCard-container .stuCard-head {
	width: 100%;
	height: 20%;
	padding: 0 0.5rem;
	background-color: #0041c2;
	display: flex;
	align-items: center;
	justify-content: flex-end;
}

.studentCard-container .stuCard-head h1 {
	font-size: 2rem;
	color: #fff;
	cursor: pointer;
}

.studentCard-container .stuCard-body {
	width: 100%;
	height: 80%;
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.studentCard-container .stuCard-body .stuCard-dp {
	position: absolute;
	top: -15%;
}

.studentCard-container .stuCard-body .stuCard-dp img {
	height: 80px;
	width: 80px;
	border-radius: 50%;
	object-fit: cover;
}

.studentCard-container .stuCard-body .stuCard-name {
	position: absolute;
	top: 18%;
	right: 0%;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.studentCard-container .stuCard-body .stuCard-name p {
	font-size: 1.2rem;
	color: blue;
}

.studentCard-container .stuCard-body .stuCard-footer {
	position: absolute;
	padding: 0 0.5rem;
	top: 30%;
	right: 0%;
	/* background-color: red; */
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
}

.studentCard-container .stuCard-body .stuCard-footer button {
	padding: 0.5rem;
	outline: none;
	background-color: transparent;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 6px;
	cursor: pointer;
	border: 1.5px solid #007bff;
	color: #007bff;
	transition: all 0.2s ease-in-out;
	font-size: 14px;
	margin: 0.25rem 0;
	width: 180px;
}

.studentCard-container .stuCard-body .stuCard-footer button:nth-child(1) {
	background-color: #007bff;
	color: #fff;
}

.studentCard-container .stuCard-body .stuCard-footer button:hover {
	background-color: #007bff;
	color: #fff;
}

.studentCard-container .stuCard-body .plus-minus-section {
	position: absolute;
	padding: 0 2rem;
	top: 65%;
	right: 0%;
	height: 30%;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.studentCard-container .stuCard-body .plus-minus-section .plus-min-dots {
	width: 100%;
	height: 50%;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.studentCard-container .stuCard-body .plus-minus-section .plus-min-dots img {
	height: 40px;
}

.studentCard-container .stuCard-body .plus-minus-section .plus-min-dots div {
	display: flex;
	align-items: center;
}

.studentCard-container .stuCard-body .plus-minus-section .plus-min-dots div img {
	height: 20px;
}

.studentCard-container .stuCard-body .plus-minus-section .plus-min-dots div span {
	font-weight: bold;
	margin: 0 7px;
}

.studentCard-container .stuCard-body .plus-minus-section .coloured-dots {
	width: 100%;
	height: 50%;
	display: flex;
	align-items: flex-end;
	justify-content: space-between;
}

.studentCard-container .stuCard-body .plus-minus-section .coloured-dots p {
	font-size: 14px;
	cursor: pointer;
}

.attendance-modal {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.attendance-modal .react-calendar {
	width: 100%;
	height: 100%;
}

.body-student {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 100%;
	justify-content: center;
}

.body-student .student-behaviour .student-behaviour-card {
	background-color: #eeeeee;
	width: 250px;
	display: flex;
	margin: 0.5rem;
	flex-direction: column;
	color: #000;
	box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.25);
}

.body-student .student-behaviour .student-behaviour-card .card-top {
	background-color: #000;
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
	color: #fff;
}

.body-student .student-behaviour .student-behaviour-card .card-top h4 {
	margin-top: 0.5rem;
	font-size: 1.2rem;
}

.body-student .student-behaviour .student-behaviour-card .card-top p {
	padding: 0.5rem;
}

.body-student .student-behaviour .student-behaviour-card .card-body {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 0.2rem;
	color: #007bff;
}

.body-student .student-behaviour .student-behaviour-card .card-body h5 {
	font-weight: bold;
	font-size: 1.2rem;
	color: #0041c2;
	display: flex;
	justify-content: center;
}

.body-student .student-behaviour .student-behaviour-card .card-body p {
	margin: 0;
	line-height: normal;
}
