.resource-container {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 1.5rem;
}

.resource-container h1 {
	font-size: 1.5rem;
	width: 100%;
}

.resource-container .resource-filter {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 0.5rem 0;
}

.resource-container .resource-filter input {
	width: 300px;
	height: 35px;
	font-size: 14px;
	padding: 0 0.25rem;
	border-radius: 5px;
	border: 2px solid lightgrey;
}

.resource-container .resource-filter .filters {
	display: flex;
	align-items: center;
}

.resource-container .resource-filter .filters .filter {
	margin: 0 0.5rem;
	border: 2px solid lightgrey;
	border-radius: 5px;
	padding: 0.25rem 1rem;
	color: rgb(44, 44, 44);
	cursor: pointer;
}

.resource-container .resource-filter .filters .filter.active-filter {
	background-color: darkgray;
	border: 2px solid darkgray;
	color: #fff;
}

.resource-container .resource-filter .filters .filter:hover {
	background-color: darkgray;
	border: 2px solid darkgray;
	color: #fff;
}

.resource-container .resource-box {
	height: 100px;
	width: 100%;
	margin-bottom: 1rem;
	cursor: pointer;
	padding: 1rem 3.5rem;
	background-color: #fff;
	border-radius: 5px;
	border: 1px solid #ddd;
	display: flex;
	justify-content: space-between;
	position: relative;
	margin: 1rem 0;
	text-decoration: none;
}

.resource-container .resource-box img {
	height: 35px;
}

.resource-container .resource-box .topic-name {
	width: 80%;
	height: 100%;
	padding: 0 1.5rem;
	display: flex;
	flex-direction: column;
}

.resource-container .resource-box .topic-name h1 {
	margin: 0;
	font-size: 18px;
	color: #333333;
}

.resource-container .resource-box .topic-name p {
	margin: 5px 0;
	font-size: 14px;
	color: #333333;
}

.resource-container .resource-box .topic-date {
	width: 20%;
	height: 100%;
	display: flex;
	align-items: flex-end;
	flex-direction: column;
}

.resource-container .resource-box .topic-date button {
	width: 120px;
	height: 30px;
	border: none;
	background-color: lightgrey;
	border-radius: 5px;
	color: rgb(44, 44, 44);
	margin-top: 10px;
}
