.my-creativity-container {
	width: 100%;
	display: flex;
	flex-direction: column;
}
.my-creativity-container .graph {
	width: 500px;
	height: 400px;
	margin: 0 auto;
}

.my-creativity-container .select-sub {
	width: 100%;
	display: flex;
	align-items: center;
}

.my-creativity-container .behaviour-chart {
	display: flex;
	flex-direction: column;
	width: 100%;
	max-height: 60vh;
	overflow-y: scroll;
	padding: 1rem;
}

.my-creativity-container .behaviour-chart .behaviour-card {
	display: flex;
	flex-direction: column;
	width: 100%;
	margin: 1rem 0;
	padding: 1rem 0;
	background-color: #fff;
	border: 1px solid #c5c5c5;
	border-radius: 10px;
}

.my-creativity-container .behaviour-chart .behaviour-card .behaviour-date {
	width: 100%;
	display: flex;
	align-items: center;
	padding: 0 1rem;
	margin-bottom: 0.5rem;
}

.my-creativity-container .behaviour-chart .behaviour-card .behaviour-list {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 100%;
	margin-bottom: 0.25rem;
}

.my-creativity-container .behaviour-chart .behaviour-card .behaviour-list .behaviour {
	display: flex;
	align-items: center;
	padding: 0.25rem 0.85rem;
	margin: 0.25rem;
	border-radius: 1rem;
	cursor: pointer;
	transition: all 0.35s ease-in-out;
}

.my-creativity-container .behaviour-chart .behaviour-card .behaviour-list .behaviour p {
	font-size: 12px;
}

.my-creativity-container .behaviour-chart .behaviour-card .behaviour-list .behaviour i {
	color: #e5e5e5;
	margin-right: 0.25rem;
	transition: all 0.35s ease-in-out;
}

.my-creativity-container .behaviour-chart .behaviour-card .behaviour-list .behaviour.active {
	display: flex;
	align-items: center;
	padding: 0.25rem 0.85rem;
	background-color: #e5e5e5;
	margin: 0.25rem;
	border-radius: 1rem;
	cursor: pointer;
}

.my-creativity-container .behaviour-chart .behaviour-card .behaviour-list .behaviour.active i {
	color: green;
	margin-right: 0.25rem;
}
