.iigp-home-container {
	width: 100%;
}

.iigp-home-container .iigp-ads-banner {
	margin-top: 16vh;
	width: 100%;
	height: 100vh;
	background-image: linear-gradient(#205C6D, #168E79);
	display: flex;
	flex-direction: column;
	margin-bottom: 1rem;
}

.iigp-home-container .iigp-ads-banner .ads-content {
	width: 100%;
	height: 30%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.iigp-home-container .iigp-ads-banner .ads-content h1 {
	color: #fff;
    font-size: 60px;
    font-weight: bold;
    font-family: Roboto, sans-serif;
	text-shadow: 0px 1px 3px rgb(150 150 150);
	margin-bottom: 1rem;
}

.iigp-home-container .iigp-ads-banner .ads-content p {
	color: #fff;
    font-size: 18px;
}

.iigp-home-container .iigp-ads-banner .ads-content button {
    padding: 0.5rem 2rem;
    font-size: 12px;
    font-weight: 500;
    text-transform: uppercase;
    color: #fff;
    background-color: #56b8a1;
    border-style: solid;
    border-width: 2px;
    border-color: #fff;
    border-radius: 36px;
	outline: none;
	margin-top: 1.5rem;
}


.iigp-home-container .iigp-ads-banner .ads-images {
	width: 100%;
	height: 70%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-image: url('./assets/banner_bg.png');
    background-repeat: no-repeat;
	background-size: cover;
	position: relative;
}

.iigp-home-container .iigp-ads-banner .ads-images .ad-plant {
	position: absolute;
	bottom: 0%;
	left: 5%;
}

.iigp-home-container .iigp-ads-banner .ads-images .ad-globe {
	position: absolute;
	bottom: 0%;
	left: 15%;
}

.iigp-home-container .iigp-ads-banner .ads-images .ad-earth {
	position: absolute;
	bottom: 10%;
	left: 16%;
	animation: rotation 10s infinite linear;
}

@keyframes rotation {
	from {
	  transform: rotate(0deg);
	}
	to {
	  transform: rotate(359deg);
	}
}

.iigp-home-container .iigp-ads-banner .ads-images .ad-bookshelf {
	position: absolute;
	top: 30%;
	left: 16%;
}

.iigp-home-container .iigp-ads-banner .ads-images .ad-women {
	position: absolute;
	bottom : 0%;
	left: 25%;
	animation: move 5s infinite linear;
}

@keyframes move {
	0% {
	  left: 25%;
	}
	50% {
	  left: 28%;
	}
	100% {
		left: 25%;
	}
}

.iigp-home-container .iigp-ads-banner .ads-images .ad-books {
	position: absolute;
	bottom : 0%;
	right: 20%;
	z-index: 2;
}

.iigp-home-container .iigp-ads-banner .ads-images .ad-teacher {
	position: absolute;
	bottom : 0%;
	left: 40%;
	animation: move2 5s infinite linear;
}

@keyframes move2 {
	0% {
	  left: 40%;
	}
	50% {
	  left: 45%;
	}
	100% {
		left: 40%;
	}
}

.iigp-home-container .iigp-ads-banner .ads-images .ad-student {
	position: absolute;
	bottom : -2%;
	right: 9%;
	animation: move3 5s infinite linear;
}

@keyframes move3 {
	0% {
		bottom: -4%;
	  	right: 7%;
	}
	50% {
		bottom: 0%;
		right: 5%;
	}
	100% {
		bottom: -4%;
	  	right: 7%;
	}
}

.iigp-home-container .iigp-ads-banner .ads-images .ad-image3 {
	position: absolute;
	bottom : 25%;
	right: 23%;
	z-index: 1;
	animation: move4 3s infinite linear;
}

@keyframes move4 {
	0% {
		bottom: 25%;
	}
	50% {
		bottom: 35%;
	}
	100% {
		bottom: 25%;
	}
}

.iigp-home-container .iigp-ads-banner .ads-images .ad-wifi {
	position: absolute;
	bottom : 20%;
	right: 30%;
	z-index: 1;
	animation: move5 4s infinite linear;
}

@keyframes move5 {
	0% {
		bottom: 20%;
	}
	50% {
		bottom: 42%;
	}
	100% {
		bottom: 20%;
	}
}

.iigp-home-container .iigp-ads-banner .ads-images .ad-image5 {
	position: absolute;
	top : 0%;
	right: 15%;
	z-index: 1;
	transform: scale(1.2);
	animation: cloud1 10s infinite linear;
}

@keyframes cloud1 {
	0% {
		right: 15%;
	}
	50% {
		right: 25%;
	}
	100% {
		right: 15%;
	}
}

.iigp-home-container .iigp-ads-banner .ads-images .ad-image6{
	position: absolute;
	top : 15%;
	left: 50%;
	z-index: 1;
	animation: cloud2 5s infinite linear;
}

@keyframes cloud2 {
	0% {
		left: 50%;
	}
	50% {
		left: 48%;
	}
	100% {
		left: 50%;
	}
}

.iigp-home-container .iigp-ads-banner .ads-images .ad-image7{
	position: absolute;
	top : 5%;
	left: 20%;
	transform: scale(1.2);
	animation: cloud3 5s infinite linear;
}

@keyframes cloud3 {
	0% {
		left: 20%;
	}
	50% {
		left: 25%;
	}
	100% {
		left: 20%;
	}
}

.iigp-home-container .iigp-about {
	width: 100%;
	margin: 3rem 0;
	display: flex;
	align-items: center;
	justify-content: center;
}

.iigp-home-container .iigp-about .iigp-about-left{
	width: 35%;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 2rem 0;
}

.iigp-home-container .iigp-about .iigp-about-left h1 {
	font-size: 42px;
	color: #333333;
	font-family: Roboto,sans-serif;
	font-weight: bold;
}

.iigp-home-container .iigp-about .iigp-about-left div {
	border-left: 2px solid #a5b7d9;
	margin: 1rem 0;
	padding: 0 1rem;
	
}

.iigp-home-container .iigp-about .iigp-about-left div p{
	color: #555555;
	font-family: Roboto,sans-serif;
}

.iigp-home-container .iigp-about .iigp-about-right{
	width: 35%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 2rem 0;
}

.iigp-home-container .iigp-about .iigp-about-right .iigp-about-box{
	background-color: #f0fdfa;
    padding: 40px 45px;
    border-style: solid;
    border-width: 2px;
    border-color: #b2eddb;
	border-radius: 15px;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.iigp-home-container .iigp-about .iigp-about-right .iigp-about-box h3 {
	margin: 0.5rem 0;
	color: #0eb582;
    font-size: 20px;
    font-weight: 800;
    line-height: 26px;
	font-family: Roboto,sans-serif;
}

.iigp-home-container .iigp-about .iigp-about-right .iigp-about-box p {
	color: #93d8c3;
font-family: "Rubik",Sans-serif;
font-size: 16px;
font-weight: 400;
letter-spacing: -.16px;
}

.iigp-home-container .iigp-feature {
	width: 100%;
	height: 100vh;
	
}

.iigp-home-container .iigp-feature img {
	height: 100vh;
	width: 100%;
}

.iigp-home-container .iigp-testimonial{
	width: 100%;
	margin-bottom: 10rem;
}

.iigp-home-container .iigp-testimonial img {
	width: 100%;
	height: auto;
}

