.my-behaviour-container {
	width: 100%;
	min-height: 80vh;
	padding: 3.5rem 0;
	display: flex;
	align-items: center;
	justify-content: center;
}

.my-behaviour-container .graph {
	width: 500px;
	height: 400px;
	margin: 0 auto;
}

.my-behaviour-container .behaviour-wrapper {
	width: 50%;
	padding: 1rem 0;
	background-color: #f5f7fa;
	border-radius: 6px;
	box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.1);
	display: flex;
	flex-direction: column;
	align-items: center;
}

.my-behaviour-container .behaviour-wrapper .select-sub {
	width: 100%;
	display: flex;
	align-items: center;
}

.my-behaviour-container .behaviour-wrapper .behaviour-chart {
	display: flex;
	flex-direction: column;
	width: 100%;
	max-height: 60vh;
	overflow-y: scroll;
	padding: 1rem;
}

.my-behaviour-container .behaviour-wrapper .behaviour-chart .behaviour-card {
	display: flex;
	flex-direction: column;
	width: 100%;
	margin: 1rem 0;
	padding: 1rem 0;
	background-color: #fff;
	border: 1px solid #c5c5c5;
	border-radius: 10px;
}

.my-behaviour-container .behaviour-wrapper .behaviour-chart .behaviour-card .behaviour-date {
	width: 100%;
	display: flex;
	align-items: center;
	padding: 0 1rem;
	margin-bottom: 0.5rem;
}

.my-behaviour-container .behaviour-wrapper .behaviour-chart .behaviour-card .behaviour-list {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 100%;
	margin-bottom: 0.25rem;
}

.my-behaviour-container .behaviour-wrapper .behaviour-chart .behaviour-card .behaviour-list .behaviour {
	display: flex;
	align-items: center;
	padding: 0.25rem 0.85rem;
	margin: 0.25rem;
	border-radius: 1rem;
	cursor: pointer;
	transition: all 0.35s ease-in-out;
}

.my-behaviour-container .behaviour-wrapper .behaviour-chart .behaviour-card .behaviour-list .behaviour p {
	font-size: 12px;
}

.my-behaviour-container .behaviour-wrapper .behaviour-chart .behaviour-card .behaviour-list .behaviour i {
	color: #e5e5e5;
	margin-right: 0.25rem;
	transition: all 0.35s ease-in-out;
}

.my-behaviour-container .behaviour-wrapper .behaviour-chart .behaviour-card .behaviour-list .behaviour.active {
	display: flex;
	align-items: center;
	padding: 0.25rem 0.85rem;
	background-color: #e5e5e5;
	margin: 0.25rem;
	border-radius: 1rem;
	cursor: pointer;
}

.my-behaviour-container .behaviour-wrapper .behaviour-chart .behaviour-card .behaviour-list .behaviour.active i {
	color: green;
	margin-right: 0.25rem;
}

.my-behaviour-container .behaviour-wrapper .behaviour-chart .behaviour-card .behaviour-list:last-child .behaviour.active i {
	color: red;
	margin-right: 0.25rem;
}
