.myschool-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  /* position: relative; */
}

.myschool-container .myschool-subject {
  width: 100%;
  margin-top: 16vh;
  display: flex;
  align-items: center;
  padding: 1rem 3.5rem;
  background-color: #4cd6c6;
  background: linear-gradient(
    90deg,
    rgba(2, 196, 215, 1) 0%,
    rgba(126, 226, 186, 1) 100%
  );
}

.myschool-container .myschool-subject p {
  color: #fff;
  font-size: 24px;
  font-weight: 500;
  text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
}

.myschool-container .myschool-activity {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100px;
  padding: 0 2rem;
  align-items: center;
  justify-content: space-around;
}

.myschool-container .myschool-activity .subject {
  display: flex;
  flex-direction: column;
  width: 180px;
  height: 90px;
  padding: 0.5rem 0;
  text-decoration: none;
  align-items: center;
  justify-content: space-around;
  cursor: pointer;
  border-radius: 15px;
  transition: all 0.3s ease-in-out;
}

.myschool-container .myschool-activity .subject.active {
  background-color: #f5f7fa;
}

.myschool-container .myschool-activity .subject:hover {
  background-color: #f5f7fa;
}

.myschool-container .myschool-activity .subject img {
  height: 40px;
}

.myschool-container .myschool-activity .subject p {
  font-size: 14px;
  font-weight: 500;
  color: #a1a1a1;
}

.myschool-container .learning-grade-breadcrumb {
  width: 100%;
  background: #e9f6ef;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.15);
  display: flex;
  align-items: center;
  padding: 1rem 3.5rem;
}

.myschool-container .learning-grade-breadcrumb p {
    color: #00b255;
    font-size: 18px;
    font-weight: bold;
    text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1);
}

.myschool-container .myschool-tabs {
  width: 100%;
  min-height: 80vh;
  background-color: #E9F6EF;
  background-image: url('./assets/download.png');
  background-size: contain;
  background-repeat: repeat;
  /* background-image: url("./assets/course_bg-2.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top; */
  display: flex;
  align-items: center;
  justify-content: center;
}