.container {
    /* margin: 0 auto; */
    /* padding: 2rem 11.8rem; */
    width: 100%;
    height: 100%;
}

.container .staticAnalytics-cont{
    padding: 0 4rem;
}
.container .staticAnalytics-cont h1{
    color: #00adef;
    font-size: 48px;
}

.container .staticAnalytics-cont h3{
    color: #929292;
    font-size: 22px;
    font-weight: 300;
}

.container .staticAnalytics-cont .box-1{
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.container .staticAnalytics-cont .box-1 .left-col{
    width: 53%;
    display: inline-block;
}

.container .staticAnalytics-cont .box-1 .left-col .benefit-content{
    padding-top: 60px;
}
.container .staticAnalytics-cont .box-1 .left-col .benefit-content h2{
    color: #4ea601;
    font-size: 24px;
    padding-bottom: 10px;
}

.container .staticAnalytics-cont .box-1 .left-col .benefit-content p{
    color: #545454;
    font-size: 15px;
    line-height: 1.47;
    margin: 0;
}

.container .staticAnalytics-cont .box-1 .right-col{
    width: 38%;
}


