.container .staticAnalytics-cont .staticAnalytics-unlock-cont h1{
    color: #222;
    font-size: 30px;
    /* font-family: "Roboto Slab",sans-serif; */
    font-weight: 300;
    padding: 60px 0 30px;
    text-align: center;
    padding-top: 30px;
}

.container .staticAnalytics-cont .staticAnalytics-unlock-cont .btn{
    display: flex;
    justify-content: center;
}

.container .staticAnalytics-cont .staticAnalytics-unlock-cont .btn button{
    min-width: 160px;
    font-size: 18px;
    height: 44px;
    line-height: 26px;
    border-style: none;
    outline: none;
    background-color: #7ebb00;
    text-decoration: none;
    border-radius: 5px;
    color: #fff;
    zoom: 1;
}

.container .staticAnalytics-cont .staticAnalytics-unlock-cont .btn button:hover{
    background-color: #9DE20E;
}