.attempt-exam-container {
	width: 100%;
	min-height: 95vh;
	background-color: #fff;
	display: flex;
}

.attempt-exam-container .thankyou-board {
	width: 100%;
	min-height: 95vh;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.attempt-exam-container .thankyou-board h3 {
	margin-top: 2rem;
}

.attempt-exam-container .question-board {
	width: 75%;
	min-height: 95vh;
	display: flex;
	align-items: center;
	justify-content: center;
}

.attempt-exam-container .question-board .question-box {
	width: 100%;
	height: 100%;
	padding: 2rem 3.5rem;
	display: flex;
	flex-direction: column;
	overflow-y: scroll;
}

.attempt-exam-container .question-board .question-box h1 {
	font-size: 1rem;
}

.attempt-exam-container .question-board .question-box p {
	font-weight: 600;
	font-size: 18px;
	color: #111111;
}

.attempt-exam-container .question-board .question-box img {
	object-fit: contain;
	width: 70%;
	height: 70%;
	margin: 1rem 0;
}

.attempt-exam-container .question-board .question-box .box-options {
	width: 100%;
	margin: 1rem 0;
	display: flex;
	flex-wrap: wrap;
}

.attempt-exam-container .question-board .question-box .box-options .options {
	border: 2px solid #04c5d7;
	border-radius: 10px;
	width: 150px;
	padding: 0.5rem 0;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	background-color: #fff;
	margin: 0 20px 20px 0;
	position: relative;
}

.attempt-exam-container .question-board .question-box .box-options .options .option-overlay {
	width: 100%;
	height: 100%;
	padding: 0.2rem;
	position: absolute;
	top: 0;
	left: 0;
	background-color: rgba(4, 197, 215, 0.1);
	display: flex;
}

.attempt-exam-container .question-board .question-box .box-options .options .option-overlay img {
	height: 20px;
	width: 20px;
}

.attempt-exam-container .question-board .question-box .box-options .options figure {
	margin: 0;
}

.attempt-exam-container .question-board .question-box .box-options .options img {
	width: 100%;
	height: 100%;
	object-fit: cover;
	margin: 0;
	border-radius: 10px;
}

.attempt-exam-container .question-board .question-box .button-options {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.attempt-exam-container .question-board .question-box .button-options #submit {
	width: 120px;
	padding: 0.5rem 0;
	background-color: #7ebb00;
	border: 2px solid #7ebb00;
	color: #fff;
	border-radius: 5px;
	outline: none;
}

.attempt-exam-container .stats-board {
	width: 25%;
	min-height: 95vh;
	padding: 1rem 0;
	background-color: #f5f7fa;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.attempt-exam-container .stats-board h1 {
	margin: 1rem 0;
	font-size: 1.2rem;
}

.attempt-exam-container .stats-board .timer-box {
	width: 85%;
	height: 15%;
	border-radius: 10px;
	background-color: #fff;
	box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
	padding: 1rem 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
}

.attempt-exam-container .stats-board .timer-box h1 {
	font-size: 1.2rem;
	color: #414141;
}

.attempt-exam-container .stats-board .section-box {
	width: 85%;
	border-radius: 10px;
	background-color: #fff;
	box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
	padding: 1rem 0;
	margin-bottom: 1rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
}

.attempt-exam-container .stats-board .section-box .section-box-head {
	width: 100%;
	height: 5vh;
	padding: 0 1rem;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.attempt-exam-container .stats-board .section-box .section-box-body {
	width: 100%;
	padding: 0;
	transition: padding 0.5s;
}

.attempt-exam-container .stats-board .section-box .section-box-body p {
	display: none;
}

.attempt-exam-container .stats-board .section-box .section-box-body.active {
	padding: 1rem 0;
	border-top: 1px solid #e1e1e1;
	display: flex;
	align-items: center;
	justify-content: center;
}

.attempt-exam-container .stats-board .section-box .section-box-body.active .question-list {
	width: 90%;
	display: flex;
	flex-wrap: wrap;
}

.attempt-exam-container .stats-board .section-box .section-box-body.active .question-list .question-circle {
	height: 30px;
	width: 30px;
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #e1e1e1;
	cursor: pointer;
	margin: 0.2rem;
}

.attempt-exam-container .stats-board .section-box .section-box-body.active .question-list .question-circle p {
	display: block;
}

.attempt-exam-container .stats-board .button-div {
	width: 85%;
	display: flex;
	margin: 1rem 0;
}

.attempt-exam-container .stats-board .button-div button {
	padding: 0.5rem 1rem;
	border-radius: 5px;
	border: none;
	outline: none;
	background-color: green;
	color: #fff;
	box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
}
