.topicList-container {
	width: 100%;
	height: 100vh;
	display: flex;
	align-items: center;
	flex-direction: column;
}

.topicList-container .topicList-header {
	width: 90%;
	margin-bottom: 1rem;
	display: flex;
	justify-content: flex-end;
}

.topicList-container .topicList-box {
	height: 150px;
	width: 90%;
	margin-bottom: 1rem;
	cursor: pointer;
	padding: 1rem 3.5rem;
	background-color: #fff;
	border-radius: 5px;
	border: 1px solid #ddd;
	display: flex;
	justify-content: space-between;
	position: relative;
}

.topicList-container .topicList-box img {
	height: 35px;
}

.topicList-container .topicList-box .topic-name {
	width: 80%;
	height: 100%;
	padding: 0 1.5rem;
	display: flex;
	flex-direction: column;
}

.topicList-container .topicList-box .topic-name h1 {
	margin: 0;
	font-size: 18px;
	color: #333333;
}

.topicList-container .topicList-box .topic-name p {
	margin: 5px 0;
	font-size: 14px;
	color: #333333;
}

.topicList-container .topicList-box .topic-date {
	width: 20%;
	height: 100%;
	display: flex;
	align-items: flex-start;
	justify-content: flex-end;
}
