.attemptQuiz-container {
    width: 100%;
    height: 83vh;
    display: flex;
}

.attemptQuiz-container .question-board {
    width: 80%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.attemptQuiz-container .question-board .correct-msg-box {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.attemptQuiz-container .question-board .correct-msg-box h1 {
    font-size: 64px;
    font-weight: bolder;
    color: #7EBB00;
}

.attemptQuiz-container .question-board .level-up-box {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.attemptQuiz-container .question-board .level-up-box h1 {
    font-weight: bold;
    color: #2CCECD;
    text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
}

.attemptQuiz-container .question-board .level-down-box {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.attemptQuiz-container .question-board .level-down-box h1 {
    font-weight: bold;
    color: #2CCECD;
    text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
}

.attemptQuiz-container .question-board .explanation-box {
    width: 100%;
    height: 100%;
    padding: 0 3.5rem;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
}

.attemptQuiz-container .question-board .explanation-box .wrong-head {
    width: 100%;
    margin-bottom: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.attemptQuiz-container .question-board .explanation-box .wrong-head div {
    width: 85%;
    display: flex;
    align-items: center;
}

.attemptQuiz-container .question-board .explanation-box .wrong-head div h1 {
    color: #a1a1a1;
}

.attemptQuiz-container .question-board .explanation-box .wrong-head #gotit {
    width: 120px;
    margin-right: 10px;
    padding: 0.5rem;
    background-color: #7EBB00;
    color: #fff;
    border-radius: 5px;
    border: 2px solid #7EBB00;
    outline: none;
}

.attemptQuiz-container .question-board .explanation-box .wrong-head #endses {
    width: 120px;
    padding: 0.5rem;
    background-color: #fff;
    border: 2px solid #CC0000;
    color: #CC0000;
    border-radius: 5px;
    outline: none;
    transition: all 0.2s ease-in-out;
}

.attemptQuiz-container .question-board .explanation-box .wrong-head #endses:hover {
    background-color: #CC0000;
    color: #fff;
}

.attemptQuiz-container .question-board .explanation-box .review-ques {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.attemptQuiz-container .question-board .explanation-box .review-ques img {
    width: 60%;
    height: 60%;
    /* margin: 1rem 0; */
    object-fit: contain;
}

.attemptQuiz-container .question-board .explanation-box .review-ques h3 {
    color: #7DE2BA;
    font-weight: bold;
}

.attemptQuiz-container .question-board .explanation-box .review-ques .box-options {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.attemptQuiz-container .question-board .explanation-box .review-ques .box-options .options {
    border: 2px solid #04C5D7;
    border-radius: 10px;
    width: 150px;
    padding: 0.5rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    background-color: #fff;
    margin: 0 20px 20px 0;
    position: relative;
}

.attemptQuiz-container .question-board .explanation-box .review-ques .box-options .options .option-overlay {
    width: 100%;
    height: 100%;
    padding: 0.2rem;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(4, 197, 215, 0.1);
    display: flex;
}

.attemptQuiz-container .question-board .explanation-box .review-ques .box-options .options .option-overlay img {
    height: 20px;
    width: 20px;
}


.attemptQuiz-container .question-board .explanation-box .review-ques .box-options .options figure {
    margin: 0;
}

.attemptQuiz-container .question-board .explanation-box .review-ques .box-options .options img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    margin: 0;
    border-radius: 10px;
}



.attemptQuiz-container .question-board .question-box {
    width: 100%;
    height: 100%;
    padding: 2rem 3.5rem;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
}

.attemptQuiz-container .question-board .question-box .button-options{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.attemptQuiz-container .question-board .question-box .button-options #submit {
    width: 120px;
    padding: 0.5rem 0;
    background-color: #7EBB00;
    border: 2px solid #7EBB00;
    color: #fff;
    border-radius: 5px;
    outline: none;
}

.attemptQuiz-container .question-board .question-box .button-options #endses {
    width: 120px;
    padding: 0.5rem 0;
    background-color: #fff;
    border: 2px solid #CC0000;
    color: #CC0000;
    border-radius: 5px;
    outline: none;
    transition: all 0.3s ease-in-out;
}

.attemptQuiz-container .question-board .question-box .button-options #endses:hover {
    background-color: #CC0000;
    color: #fff;
}

.attemptQuiz-container .question-board .question-box p {
    font-weight: 600;
    font-size: 18px;
    color: #111111;
}

.attemptQuiz-container .question-board .question-box img {
    object-fit: contain;
    width: 70%;
    height: 70%;
    margin: 1rem 0;
}

.attemptQuiz-container .question-board .question-box .box-options {
    width: 100%;
    margin: 1rem 0;
    display: flex;
    flex-wrap: wrap;
}


.attemptQuiz-container .question-board .question-box .box-options .options {
    border: 2px solid #04C5D7;
    border-radius: 10px;
    width: 150px;
    padding: 0.5rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    background-color: #fff;
    margin: 0 20px 20px 0;
    position: relative;
}

.attemptQuiz-container .question-board .question-box .box-options .options .option-overlay {
    width: 100%;
    height: 100%;
    padding: 0.2rem;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(4, 197, 215, 0.1);
    display: flex;
}

.attemptQuiz-container .question-board .question-box .box-options .options .option-overlay img {
    height: 20px;
    width: 20px;
}


.attemptQuiz-container .question-board .question-box .box-options .options figure {
    margin: 0;
}

.attemptQuiz-container .question-board .question-box .box-options .options img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    margin: 0;
    border-radius: 10px;
}


.attemptQuiz-container .stats-board {
    width: 20%;
    height: 100%;
    padding: 1rem 0;
    background-color: #f5f7fa;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.timer-box {
    padding: 1rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.timer-box h1 {
    font-size: 24px;
    font-weight: bold;
    color: #414141;
}

.attemptQuiz-container .stats-board .stat-box {
    width: 85%;
    height: 20%;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0px 2px 10px 0px rgba(0,0,0,0.1);
    display: flex;
}

.attemptQuiz-container .stats-board .stat-box .left {
    width: 50%;
    height: 100%;
    border-radius: 10px 0 0 10px;
    display: flex;
    align-items: center;
    padding: 1rem;
}

.bg-green {
    background-color: #7EBB00;
}

.bg-red {
    background-color: #E76836;
}

.bg-blue {
    background-color: #1FB2E4;
}

.attemptQuiz-container .stats-board .stat-box .left p {
    color: #fff;
    font-weight: bold;
    text-align: center;
    width: 100%;
}

.attemptQuiz-container .stats-board .stat-box .right {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 1rem;
}

.attemptQuiz-container .stats-board .stat-box .right h1 {
    text-align: center;
    width: 100%;
}

/* End quiz  */

.endQuiz-container {
    width: 100%;
    height: 83vh;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.endQuiz-container .time-stat {
    width: 100%;
    padding: 1rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.endQuiz-container .data-stat {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.endQuiz-container .data-stat .stat-box {
    width: 30%;
    height: 100px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0px 2px 10px 0px rgba(0,0,0,0.1);
    display: flex;
}

.endQuiz-container .data-stat .stat-box .left {
    width: 50%;
    height: 100%;
    border-radius: 10px 0 0 10px;
    display: flex;
    align-items: center;
    padding: 1rem;
}

.endQuiz-container .data-stat .stat-box .left p {
    color: #fff;
    font-weight: bold;
    text-align: center;
    width: 100%;
}

.endQuiz-container .data-stat .stat-box .right {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 1rem;
}

.endQuiz-container .data-stat .stat-box .right h1 {
    text-align: center;
    width: 100%;
}